// @ts-ignore
import bridgeLogoSvg from "../../../assets/img/logo/bridge-logo.png";
// @ts-ignore
import icxLogoSvg from "../../../assets/img/logo/icon-logo.svg";
import {environment} from "../../../environment/environment";

export class Token {
    tag: string;
    name: string;
    logo: any;
    scoreAddress: string;
    balance: number = 0;

    constructor(tag: string, name: string, logo: any, scoreAddress: string, balance?: number) {
        this.tag = tag;
        this.name = name;
        this.logo = logo;
        this.scoreAddress = scoreAddress;
        if (balance)
            this.balance = balance;
    }
}

export enum SupportedTokens {
    USDb = "USDb",
    ICX = "ICX"
}

export class Tokens {
    public static supportedTokensMap: Map<string,Token> = new Map([
        [SupportedTokens.USDb, new Token("USDb", "Bridge Dollars", bridgeLogoSvg, environment.BRIDGE_SCORE_ADDRESS)],
        [SupportedTokens.ICX, new Token("ICX", "ICON", icxLogoSvg, "")],
    ]);
}
