import {
    validateDateString, validateFileExtension,
    validateName, validatePhoneCountry,
    validatePhoneNumber, validatePrimaryAddressCountry, validatePrimaryAddressRegionUS, validateStreet,
    validateTaxIdNumber, validateUsPostalCode,
    validateUSstateCode
} from "./account-validation";
import {containsOnlyDigits} from "./validation";

export function validateKycIdentityViewFields(name: string | undefined, dateOfBirth: string | undefined,
                                              taxIdNumber:string | undefined, taxState:string | undefined, phoneNumber: string | undefined,
                                              phoneCountry:string | undefined, contactHasUSTaxInfo: boolean): string[] {
    let errors: string[] = [];

    // *name
    if(!validateName(name)) {
        errors.push("Invalid name");
    }
    // *date-of-birth
    if (!validateDateString(dateOfBirth)) {
        errors.push("Invalid date of birth (format must be: dd-mm-yyyy e.g. 1970-01-01)");
    }
    // *tax-id-number
    if (contactHasUSTaxInfo && !validateTaxIdNumber(taxIdNumber)) {
        errors.push("Invalid ID number");
    }
    // *tax-state
    if (!validateUSstateCode(taxState)) {
        errors.push("Invalid Tax state code");
    }
    if (!validatePhoneNumber(phoneNumber)) {
        errors.push("Invalid phone number");
    }
    if (!validatePhoneCountry(phoneCountry)) {
        errors.push("Invalid phone country");
    }

    return errors;
}

export function validateKycAddressViewFields(street: string | undefined, city: string | undefined, country:string | undefined,
                                             region: string | undefined, postalCode:string | undefined, hasUSAddress: boolean ): string[] {
    let errors: string[] = [];

    if(!validateStreet(street)) {
        errors.push("street");
    }
    if (!city || containsOnlyDigits(city)) {
        errors.push("city");
    }
    if (!validatePrimaryAddressCountry(country)) {
        errors.push("country");
    }
    if (hasUSAddress && !validatePrimaryAddressRegionUS(region)) {
        errors.push("region");
    }
    if (hasUSAddress && !validateUsPostalCode(postalCode)) {
        errors.push("postalCode");
    }
    if (hasUSAddress && country != "US") {
        errors.push("Has US address but not US as country");
    }

    return errors;
}

export function validateDocumentsUploadView(documentFrontFile: FileList | null, documentBackFile: FileList | null,
                                            documentProofOfAddress: FileList | null, selectedIdDocumentType: string, other: string): string[] {
    let errors: string[] = [];

    if (documentFrontFile && documentFrontFile.length > 0) {
        if(!validateFileExtension(documentFrontFile![0].name)) {
            errors.push("documentFrontFile invalid extension")
        }
    } else {
        errors.push("documentFrontFile missing")
    }
    // driver license requires backside photo
    if (selectedIdDocumentType == "drivers_license") {
        if (documentBackFile && documentBackFile.length > 0) {
            if(!validateFileExtension(documentBackFile![0].name)) {
                errors.push("documentBackFile invalid extension")
            }
        } else {
            errors.push("documentBackFile missing")
        }
    }
    if (selectedIdDocumentType == "other") {
        if(!other) {
            errors.push("other missing or empty")
        }
    }
    if (documentProofOfAddress && documentProofOfAddress.length > 0) {
        if(!validateFileExtension(documentProofOfAddress![0].name)) {
            errors.push("documentProofOfAddress invalid extension")
        }
    }
    return errors;
}
