export function formatAndFilterIcxTransactions(icxTransactions: IcxTransactions, userIconWalletAddress: string): IcxTransaction[] {
    log("formatAndFilterIcxTransactions-> icxTransactions:", icxTransactions)
    if (icxTransactions && icxTransactions.data.length > 0) {
        let transactions: IcxTransaction[] = [];
        let date: Date;
        icxTransactions.data.forEach((icxTransaction: IcxTransaction) => {
            if (icxTransaction.state == 1 && icxTransaction.dataType == "icx") {
                date = new Date(icxTransaction.createDate);
                transactions.push(new IcxTransaction(+icxTransaction.amount, icxTransaction.createDate, icxTransaction.dataType,
                    icxTransaction.fromAddr, icxTransaction.state, icxTransaction.toAddr,icxTransaction.txHash, icxTransaction.txType, date.toDateString(), date,
                    userIconWalletAddress == icxTransaction.fromAddr ? TransactionType.SENT : TransactionType.RECEIVED,));
            }
        })
        log("Returning transactions: ", transactions);
        return transactions.sort((a: IcxTransaction, b: IcxTransaction) => b.date.getTime() - a.date.getTime());
    } else {
        return [];
    }
}

// filter out non Bridge token transactions as well as failed ones , format date, set type of transaction and sort by date
export function formatAndFilterBridgeTransactions(iconTokenTransactions: Irc2TokenTransaction[] | undefined, deposits: Deposit[] | undefined,
                                                  userIconWalletAddress: string, withdrawals: Withdrawal[] | undefined): BridgeTransaction[] {

    let transactions: BridgeTransaction[] = [];
    let date: Date;

    if (iconTokenTransactions) {
        iconTokenTransactions.forEach((transaction:Irc2TokenTransaction) => {
            if (transaction.contractSymbol != CodeLists.BRIDGE_SYMBOL || transaction.state != 1 || transaction.contractAddr != environment.BRIDGE_SCORE_ADDRESS) return;
            // create Transaction object and push it to accumulated transaction list
            else {
                date = new Date(transaction.createDate);
                transactions.push(new BridgeTransaction(date.toDateString(), date, +transaction.quantity, transaction, undefined,
                    userIconWalletAddress == transaction.fromAddr ? TransactionType.SENT : TransactionType.RECEIVED, undefined
                ));
            }
        });
    }

    if (deposits) {
        deposits.forEach((deposit: Deposit) => {
            date = new Date(deposit.createdAt);
            transactions.push(new BridgeTransaction(date.toDateString(), date, +deposit.amount,undefined,
                deposit, TransactionType.DEPOSITED, undefined));
        })
    }
    if (withdrawals) {
        withdrawals.forEach((withdrawal: Withdrawal) => {
            date = new Date(withdrawal.createdAt);
            transactions.push(new BridgeTransaction(date.toDateString(), date, +withdrawal.amount,undefined,
                undefined, TransactionType.WITHDRAW, withdrawal));
        })
    }

    return transactions.sort((a: BridgeTransaction, b: BridgeTransaction) => b.date.getTime() - a.date.getTime())
}

export function mapFundsTransferMethodToBank(fundsTransferMethod: any): BankAccount {
    return new BankAccount(
        fundsTransferMethod["id"],
        fundsTransferMethod["attributes"]["contact-name"],
        fundsTransferMethod["attributes"]["bank-name"],
        fundsTransferMethod["attributes"]["bank-account-name"],
        fundsTransferMethod["attributes"]["bank-account-name"],
        fundsTransferMethod["attributes"]["routing-number"],
        fundsTransferMethod["attributes"]["last-4"],
        fundsTransferMethod["attributes"]["funds-transfer-type"]
    )
}

export function mapFundsTransferMethodToCreditCard(fundsTransferMethod: any): CreditCard {
    return new CreditCard(
        fundsTransferMethod["id"],
        fundsTransferMethod["attributes"]["credit-card-name"],
        fundsTransferMethod["attributes"]["credit-card-postal-code"],
        fundsTransferMethod["attributes"]["credit-card-expiration-date"],
        fundsTransferMethod["attributes"]["credit-card-type"],
        fundsTransferMethod["attributes"]["last-4"],
        fundsTransferMethod["attributes"]["funds-transfer-type"]
    )
}

export function transactionTypeToClass(transaction: BridgeTransaction): string {
    switch (transaction.type) {
        case TransactionType.DEPOSITED:
            return "deposited";
        case TransactionType.WITHDRAW:
            if (transaction.withdrawTransaction?.status == "pending") {
                return "withdrawing";
            } else {
                return "withdrawn";
            }
        case TransactionType.RECEIVED:
            return "deposited";
        case TransactionType.SENT:
            return "sent";
    }
}

export function tokenTransactionTypeToClass(transaction: IcxTransaction) {
    switch (transaction.type) {
        case TransactionType.RECEIVED:
            return "deposited";
        case TransactionType.SENT:
            return "sent";
        default:
            return "sent";
    }
}

export function getTransactionName(transaction: BridgeTransaction): string  {
    if (transaction.type == TransactionType.WITHDRAW && transaction.withdrawTransaction?.status == "pending") {
        return "Withdrawing...";
    } else {
        return transaction.type.valueOf();
    }
}

export function getKycStatusTitle(userKycData: UserKycData | undefined, title: string = "") {
    if (title == "") {
        if (!userKycData) return "Not verified";
        if (!(userKycData.status == KycStatus.opened && userKycData.cipCleared)) {
            return "Not verified";
        } else if (!userKycData.amlCleared) {
            return "Verifying";
        } else {
            return "Verified";
        }
    } else {
        if (!userKycData) return "Not verified";
        if (title == "Not verified") {
            return "Not verified";
        } else if (title == "Verifying") {
            return "Verifying";
        } else {
            return "Verified";
        }
    }
}

export function getKycStatusClass(title: string, userKycData: UserKycData | undefined) {
    if (!userKycData) return "not-verified";
    if (title == "Not verified") {
        return "not-verified";
    } else if (title == "Verifying") {
        return "verifying";
    } else {
        return "";
    }
}

export function getKycFailureMessage(errorMessage: string, country: string | undefined): string {
    switch (errorMessage) {
        case CipExceptions.NameAutoCheck:
            return "Couldn't verify your name.";
        case CipExceptions.DateOfBirthAutoCheck:
            return "Couldn't verify your date of birth.";
        case CipExceptions.TaxIdAutoCheck:
            if (country == "US")
                return "Couldn't verify your tax ID number";
            else
                return "Couldn't verify your ID number";
        case CipExceptions.TaxIdManualCheck:
            if (country == "US")
                return "Couldn't verify your tax ID number";
            else
                return "Couldn't verify your ID number";
        case CipExceptions.AddressCheck:
            return "Couldn't verify your address.";
        case CipExceptions.AddressManualCheck:
            return "Couldn't verify your address.";
        case KycDocCheckExceptions.DateOfBirth:
            return "Your date of birth and photo ID don't match";
        case KycDocCheckExceptions.DocumentInvalid:
            return "Your photo ID is not valid";
        case KycDocCheckExceptions.WaitingForDocumentUploads:
            return "Your documents did not get uploaded"
        case KycDocCheckExceptions.TaxCountry:
            return "Your country and photo ID don't match";
        case KycDocCheckExceptions.AddressAutoCheck:
            return "Your address and proof of address don't match.";
        default:
            return "";
    }
}
