export class StandardKycDocumentCheck {
    // uuidV4
    uploadedDocumentId: string; // * required
    // uuidV4
    backsideDocumentId: string | undefined;
    //  (Date (on or after today))
    expiresOn: string | undefined;
    identity: boolean;
    identityPhoto: boolean;
    proofOfAddress: boolean;
    kycDocumentCountry: string; // * required
    // KYC Document Type
    kycDocumentType: string; // * required


    constructor(uploadedDocumentId: string, kycDocumentCountry: string, kycDocumentType: string,identity: boolean, identityPhoto: boolean
        , proofOfAddress: boolean, backsideDocumentId?: string, expiresOn?: string) {
        this.uploadedDocumentId = uploadedDocumentId;
        this.backsideDocumentId = backsideDocumentId;
        this.expiresOn = expiresOn;
        this.identity = identity;
        this.identityPhoto = identityPhoto;
        this.proofOfAddress = proofOfAddress;
        this.kycDocumentCountry = kycDocumentCountry;
        this.kycDocumentType = kycDocumentType;
    }
}
