
export class User {

    email: string;
    didEthrWalletAddress: string | null;
    iconWalletAddress: string | null;
    primeTrustAccId: string | null | undefined;
    primeTrustContactId: string | null | undefined;


    constructor(email: string, didEthrWalletAddress: string | null, iconWalletAddress: string | null, primeTrustAccId?: string | null, primeTrustContactId?: string | null) {
        this.email = email;
        this.didEthrWalletAddress = didEthrWalletAddress;
        this.iconWalletAddress = iconWalletAddress;
        this.primeTrustAccId = primeTrustAccId;
        this.primeTrustContactId = primeTrustContactId;
    }
}
