import {LitElement, html, customElement, property} from 'lit-element';
import {BridgeService} from "../../lib/BridgeService";
import {Consts} from "../../lib/common/consts";
import {User} from "../../lib/models/User/User";
import {hideModalView} from "../logic/view-transitions";
import {validateDocumentsUploadView} from "../../lib/validation/kyc-validation";
import {KycDocument} from "../../lib/models/PrimeTrust/KycDocument";
import {extractFileExtension, log} from "../../lib/common/Utils";
import {StandardKycDocumentCheck} from "../../lib/models/PrimeTrust/StandardKycDocumentCheck";
import {CodeLists} from "../../lib/common/code-lists";
import {StandardKycDocumentCheckOther} from "../../lib/models/PrimeTrust/StandardKycDocumentCheckOther";

// css styles
// @ts-ignore
import mainCSS from '../../assets/css/main.css';
// @ts-ignore
import processingSvg from "../../assets/img/icon/processing.svg"
// @ts-ignore
import paymentDeclinedSvg from "../../assets/img/icon/payment-declined.svg"
// @ts-ignore
import paymentSuccessSvg from "../../assets/img/icon/payment-successful.svg";

const uploadingDocumentsTitle = "Uploading your documents..";

@customElement('kyc-documents')
class KycDocuments extends LitElement {

    @property() bridge: BridgeService | undefined;
    @property() user: User | undefined;

    @property() activeView: HTMLElement | null | undefined;

    @property() spinnerTitle: string = Consts.spinnerTransferingTitle;

    @property() selectedDocumentType: string = "passport";

    // views
    private kycDocumentsView : HTMLElement | null | undefined;
    private processingModalView: HTMLElement | null | undefined;
    private kycUploadErrorView: HTMLElement | null | undefined;
    private kycUploadSuccessView: HTMLElement | null | undefined;

    private documentFrontSide: HTMLInputElement | undefined;
    private documentBackSide: HTMLInputElement | undefined;
    private documentProofOfAddress: HTMLInputElement | undefined;
    private documentOther: HTMLInputElement | undefined;
    private documentCountry: HTMLInputElement | undefined;
    private documentType : HTMLInputElement | undefined;

    @property() errors: string[] = [];

    protected firstUpdated(_changedProperties: any): void {
        super.firstUpdated(_changedProperties);

        this.kycDocumentsView = this.shadowRoot!.getElementById("kyc-documents-view");
        this.processingModalView = this.shadowRoot!.getElementById("kyc-documents-processing");
        this.kycUploadErrorView = this.shadowRoot!.getElementById("kyc-documents-error");
        this.kycUploadSuccessView = this.shadowRoot!.getElementById("kyc-documents-success");

        this.documentFrontSide =  <HTMLInputElement>this.shadowRoot!.getElementById("doc-front-side");
        this.documentBackSide =  <HTMLInputElement>this.shadowRoot!.getElementById("doc-back-side");
        this.documentProofOfAddress = <HTMLInputElement>this.shadowRoot!.getElementById("upload-proof-of-address");
        this.documentType =  <HTMLInputElement>this.shadowRoot!.getElementById("document-type");
        this.documentOther = <HTMLInputElement>this.shadowRoot!.getElementById("kyc-document-other");
        this.documentCountry = <HTMLInputElement> this.shadowRoot!.getElementById("account-document-country");


        this.selectedDocumentType =  "passport";
        this.showModalView(this.kycDocumentsView);
    }

    constructor() {
        super();
    }

    static styles = [
        mainCSS
    ];

    async submitUploadDocumentsForm(event:Event) {
        event.preventDefault();

        // validate form / files
        this.errors = validateDocumentsUploadView(this.documentFrontSide!.files, this.documentBackSide!.files,
            this.documentProofOfAddress!.files, this.selectedDocumentType, this.documentOther ? this.documentOther.value: "");
        if(this.errors.length > 0) {
            this.showModalView(this.kycUploadErrorView);
            return false;
        }

        // show spinner
        this.showModalView(this.processingModalView, uploadingDocumentsTitle);

        // init / reset variables
        const documentsToUpload: File[] = [this.documentFrontSide!.files![0], this.documentProofOfAddress!.files![0]];
        if (this.selectedDocumentType == "drivers_license") documentsToUpload.push(this.documentBackSide!.files![0]);

        let kycDocument: KycDocument;
        let errors: string[] = [];

        for (const [index, file] of documentsToUpload.entries()) {
            kycDocument = new KycDocument(extractFileExtension(file));
            await this.bridge!.kycApiService.postAccountDocument(kycDocument, file)
                .then((res:any) => {
                    log("Document uploaded:", file.name);
                    const documentId = res.data.data.id;
                    let kycDocumentCheck: StandardKycDocumentCheck;
                    const docCountry = this.documentCountry!.value;
                    // if document is proof of address
                    if (index == 1) {
                        kycDocumentCheck = new StandardKycDocumentCheck(documentId, docCountry, CodeLists.kycProofOfAddressDocumentType,
                            false,false,true);
                    }
                    else {
                        if (this.selectedDocumentType == "other")
                            kycDocumentCheck = new StandardKycDocumentCheckOther(documentId, docCountry, this.documentType!.value,
                                this.documentOther!.value, true, true, false);
                        else
                            kycDocumentCheck = new StandardKycDocumentCheck(documentId, docCountry, this.documentType!.value,
                                true,true,false);
                    }
                    this.bridge!.kycApiService.postKycDocumentChecks(kycDocumentCheck).then((res:any) => {
                        log("Kyc document check result:", res);
                    }).catch((reason:any) => {
                        log("Kyc document check error:", reason);
                        errors.push(reason.toString())
                    })
                })
                .catch((reason:any) => {
                    log(reason);
                    errors.push(reason.toString())
                });
        }
        this.errors = errors;
        if (this.errors.length > 0) {   // show error view with the list of errors
            this.showModalView(this.kycUploadErrorView);
        } else {    // show success view
            this.showModalView(this.kycUploadSuccessView);
        }
        return false;
    }

    private documentIdTypeChange() {
        this.selectedDocumentType = this.documentType!.value;
        log("Changed document type to: ", this.selectedDocumentType);
    }

    private showModalView(modalView: HTMLElement | null | undefined, title?: string) {
        if (this.activeView)
            hideModalView(this.activeView);
        if(title) {
            this.spinnerTitle = title;
        }
        modalView!.style.display = "block";
        this.setActiveModalView(modalView);
    }

    private setActiveModalView(newActiveModal: HTMLElement | null | undefined) {
        this.activeView = newActiveModal;
        log("New kyc documents active modal:", newActiveModal);
    }

    private backToHomeViewEvent(){
        this.showModalView(this.kycDocumentsView);
        let event = new CustomEvent('backToHomeView', {});
        this.dispatchEvent(event);
    }

    protected render(): unknown {
        return html`
                  <!-- KYC Verify address: Upload proof -->
                  <div class="widget" id="kyc-documents-view" hidden>
                    <!-- Navigation -->
                    <div class="grid">
                      <div class="grid-cell">
                        <a @click="${this.backToHomeViewEvent}"  class="back">Back</a>
                      </div>
                      <div class="grid-cell">
                        <p class="label-upper text-center">KYC</p>
                      </div>
                      <div class="grid-cell">
                
                      </div>
                    </div>
                    <form class="margin-top-25 square-input" @submit=${(e:Event) => this.submitUploadDocumentsForm(e)}>
                        <div class="grid">
                          <div class="grid-cell margin-right-25">
                            <label for="document-type">Document type</label><br>
                            <select @change=${() => this.documentIdTypeChange()} name="document-type" class="margin-top-10 margin-bottom-20" id="document-type">
                                ${Array.from(CodeLists.kycDocumentsTypesPrettyMap).map(([key, value]) => html `<option value="${key}">${value}</option>`)}
                            </select>
                          </div>
                          <div class="grid-cell">
                            <label for="account-document-country">Document country</label><br>
                            <select name="account-document-country" class="margin-top-10 margin-bottom-20" id="account-document-country" required>
                                ${CodeLists.countryCodes.map(code => html `<option value="${code.code}" label="${code.name}">${code.name}</option>`)}
                            </select>
                          </div>
                        </div>
                        
                        <div class="grid">
                          <div class="grid-cell margin-right-25" ?hidden=${this.selectedDocumentType != "other"}>
                            <label for="kyc-document-other"></label>Other document type<br>
                            <input class="margin-top-10 margin-bottom-20 text-center" type="text" id="kyc-document-other" name="kyc-document-other" ?required=${this.selectedDocumentType == "other"}>
                          </div>
                          <div class="grid-cell" ?hidden=${this.selectedDocumentType != "other"}>
                          
                          </div>
                        </div>
                                        
                        <label for="doc-front-side">Upload front side photo of the document</label><br>
                        <input class="margin-top-10 margin-bottom-20 text-center" type="file" id="doc-front-side" name="doc-front-side" required>
                        
                        <label for="doc-back-side" 
                        ?hidden="${(this.selectedDocumentType != "drivers_license")}">Upload backside photo of the document</label><br>
                        <input class="margin-top-10 margin-bottom-20 text-center" type="file" id="doc-back-side" name="doc-back-side" ?required=${this.selectedDocumentType == "drivers_license"}
                        ?hidden="${(this.selectedDocumentType != "drivers_license")}">
                    
                      <label for="upload-proof-of-address">Upload proof of your address, like a utility bill or bank statement.</label><br>
                      <input class="margin-top-10 text-center" type="file" id="upload-proof-of-address" name="upload-proof-of-address" required>
                        <div class="grid grid-center margin-bottom-10 margin-top-25">
                          <div class="grid-cell text-center margin-right-25">
                            <input type="submit" value="Upload" class="button">
                          </div>
                        </div>
                    </form>
                  </div>
                  
                <!-- KYC processing -->
                <div>
                  <div class="widget" id="kyc-documents-processing" hidden>
                    <div class="grid grid-center inherit-height">
                      <div class="grid-cell">
                        <img src="${processingSvg}" class="feature-icon spinSvg">
                        <h2>${this.spinnerTitle}</h2>
                        <p class="text-center margin-top-5">This may take a few moments.</p>
                      </div>
                    </div>
                  </div>
                </div>
                
                <!-- KYC upload of documents successful -->
                <div class="widget" id="kyc-documents-success" hidden>
                <!-- Navigation -->
                <div class="grid">
                  <div class="grid-cell">
                      <a @click="${this.backToHomeViewEvent}" class="back">Back</a>
                      </div>
                      <div class="grid-cell">
                        <p class="label-upper text-center">KYC</p>
                      </div>
                      <div class="grid-cell">
                  </div>
                  
                </div>
                  <div class="grid grid-center inherit-height">
                    <div class="grid-cell">
                      <img src=${paymentSuccessSvg} class="feature-icon">
                      <h2>KYC Documents uploaded</h2>
                      <p class="text-center margin-top-5">KYC Documents have been successfully uploaded.</p>
                    </div>
                  </div>
                </div>    
                
                <!-- Kyc upload of documents error -->
                <div class="widget" id="kyc-documents-error" hidden>
                <!-- Navigation -->
                <div class="grid">
                  <div class="grid-cell">
                      <a @click="${() => {this.showModalView(this.kycDocumentsView)}}" class="back">Back</a>
                      </div>
                      <div class="grid-cell">
                        <p class="label-upper text-center">KYC</p>
                      </div>
                      <div class="grid-cell">
                  </div>
                 </div>
                  <div class="grid grid-center inherit-height">
                    <div class="grid-cell">
                      <img src=${paymentDeclinedSvg} class="feature-icon">
                      <h2>KYC Document upload error</h2>
                        ${this.errors.map((error: string) => html `
                        <p class="text-center margin-top-5">${error}</p></br>
                        `)}
                    </div>
                  </div
        `
    }
}

declare global {
    interface HTMLElementTagNameMap {
        'kyc-documents': KycDocuments;
    }
}
