import {PrimaryAddressUS} from "./PrimaryAddressUS";
import {PrimaryPhoneNumber} from "./PrimaryPhoneNumber";

export class NaturalPersonContactNonUS {

    email: string; // *required
    name: string; // *required
    primaryAddress: PrimaryAddressUS;  // *required
    primaryPhoneNumber: PrimaryPhoneNumber;  // *required
    // TODO possibly change to also enable companies???
    contactType: string = "natural_person";  // *required
    // Date (before today) e.g. "1980-06-09" -> yyyy-mm-dd
    dateOfBirth: string;  // *required
    // enum "male", "female" or "other"
    sex: string | undefined;
    taxCountry: string; // *required
    taxIdNumber: string; // *required (for US Tax ID (SSN or EIN) regex = ^\d{9}$)
    // Array of strings <uuid> (UUIDv4)
    // uploadedDocIds: string[] | undefined;
    // Array of Standard KYC Document Type (object) or Other KYC Document Type (object) (Kyc Document Checks: Inline Create)
    // kycDocumentChecks: (StandardKycDocumentCheck | StandardKycDocumentCheckOther) [] | undefined;


    constructor(email: string, name: string, primaryAddress: PrimaryAddressUS, primaryPhoneNumber: PrimaryPhoneNumber,
                dateOfBirth: string, sex: string | undefined, taxCountry: string, taxIdNumber: string) {
        this.email = email;
        this.name = name;
        this.primaryAddress = primaryAddress;
        this.primaryPhoneNumber = primaryPhoneNumber;
        this.contactType = "natural_person";
        this.dateOfBirth = dateOfBirth;
        this.sex = sex;
        this.taxCountry = taxCountry;
        this.taxIdNumber = taxIdNumber;
    }
}
