import {StandardKycDocumentCheck} from "./StandardKycDocumentCheck";

export class StandardKycDocumentCheckOther extends StandardKycDocumentCheck {
    kycDocumentOtherType: string;


    constructor(uploadedDocumentId: string, kycDocumentCountry: string, kycDocumentType: string, kycDocumentOtherType: string,
                identity: boolean, identityPhoto: boolean, proofOfAddress: boolean, backsideDocumentId?: string, expiresOn?: string) {
        super(uploadedDocumentId, kycDocumentCountry, kycDocumentType, identity, identityPhoto, proofOfAddress, backsideDocumentId, expiresOn);
        this.kycDocumentOtherType = kycDocumentOtherType;
    }
}
