import {PrimaryAddressUS} from "./PrimaryAddressUS";
import {PrimaryPhoneNumber} from "./PrimaryPhoneNumber";
import {NaturalPersonContactNonUS} from "./NaturalPersonContactNonUS";

export class NaturalPersonContactUS extends NaturalPersonContactNonUS{
    // US State Codes
    taxState: string;


    constructor(email: string, name: string, primaryAddress: PrimaryAddressUS, primaryPhoneNumber: PrimaryPhoneNumber,
                dateOfBirth: string, sex: string | undefined, taxIdNumber: string, taxState: string) {
        super(email,name, primaryAddress, primaryPhoneNumber, dateOfBirth, sex, "US", taxIdNumber)
        this.taxState = taxState;
    }
}
