import {customElement, html, LitElement, property} from "lit-element";

// @ts-ignore
import mainCSS from "../../assets/css/main.css";
import {BridgeService} from "../../lib/BridgeService";
import {User} from "../../lib/models/User/User";
import {hideModalView} from "../logic/view-transitions";

// @ts-ignore
import paymentSuccessSvg from "../../assets/img/icon/payment-successful.svg";
// @ts-ignore
import paymentDeclinedSvg from "../../assets/img/icon/payment-declined.svg";
// @ts-ignore
import dollarGreenSvg from "../../assets/img/icon/dollar-in-green.svg";

import {validateContribution} from "../../lib/validation/deposit-validation";
import {
    calculateCreditCardFee,
    extractDetailMessageFromError,
    extractMessageFromError,
    extractPaymentMethodName,
    isBankAccount,
    log, numberWithCommas
} from "../../lib/common/Utils";
import {BankAccount} from "../../lib/models/bankAccount/BankAccount";
import {AxiosResponse} from "axios";
import {IstaBalanceCheck} from "../../lib/models/Util/ista-balance-check";
import {BridgeError} from "../../lib/models/errors/bridgeError";
import {CreditCard} from "../../lib/models/CreditCard/CreditCard";
import {hideSpanError, showSpanError} from "../logic/utils";
import {amountContainsMaxOneDotFollowedBy2decimals} from "../../lib/validation/validation";


@customElement('deposit-elem')
class DepositElem extends LitElement {

    @property() bridge: BridgeService | undefined;
    @property() user: User | undefined;
    @property() userBalance: number = 0;

    @property() private activeView: HTMLElement | null | undefined;
    @property() private depositAmountView: HTMLElement | null | undefined;
    @property() private depositConfirmationView: HTMLElement | null | undefined;
    @property() private processingView: HTMLElement | null | undefined;
    @property() private paymentSuccessView: HTMLElement | null | undefined;
    @property() private paymentDeclinedView: HTMLElement | null | undefined;


    @property() private amountInput: HTMLInputElement | null | undefined;
    @property() private amountInputError: HTMLElement | undefined | null;

    @property() spinnerTitle: string = "Processing your payment...";

    @property() private selectedPaymentMethod: BankAccount | CreditCard = new BankAccount("","","","","","","","");

    @property() private amount: number = 0.00;
    @property() private feeAmount: number = 0.00;
    @property() private totalAmount: number = 0.00;


    @property() private errors: string[] = [];


    constructor() {
        super();
    }

    static styles = [
        mainCSS
    ];

    protected firstUpdated(_changedProperties: any): void {
        super.firstUpdated(_changedProperties);

        this.depositAmountView = this.shadowRoot!.getElementById("deposit-amount");
        this.amountInput = <HTMLInputElement>this.shadowRoot!.getElementById("deposit-amount-input");
        this.amountInputError = this.shadowRoot!.getElementById("amount-error");
        this.depositConfirmationView = this.shadowRoot!.getElementById("deposit-confirmation");
        this.paymentSuccessView = this.shadowRoot!.getElementById("payment-successful");
        this.paymentDeclinedView = this.shadowRoot!.getElementById("payment-declined");

        this.processingView = this.shadowRoot!.getElementById("processing-payment");

        this.showModalView(this.depositAmountView);
    }

    private showModalView(modalView: HTMLElement | null | undefined, title?: string) {
        hideModalView(this.activeView)
        if(title) {
            this.spinnerTitle = title;
        }
        modalView!.style.display = "block";
        this.setActiveModalView(modalView);
    }

    private setActiveModalView(newActiveModal: HTMLElement | null | undefined) {
        this.activeView = newActiveModal;
    }

    backToPaymentMethods() {
        this.resetFields();
        this.dispatchEvent(new CustomEvent('backToPaymentMethods', {}));

        // reset view
        this.showModalView(this.depositAmountView);
    }

    private backToHomeView() {
        this.resetFields();
        this.dispatchEvent(new CustomEvent('backToHomeView', {}));
        // reset view
        this.showModalView(this.depositAmountView);
    }

    private amountChange(event: InputEvent) {
        // skip for arrow keys
        if (event.which >= 37 && event.which <= 40){
            return;
        }

        this.amountInput!.value = this.amountInput!.value.split(",").join("");

        this.amount = +this.amountInput!.value;
        if (this.amount < 0) {
            this.clearInputFields();
            return;
        }
        this.feeAmount = isBankAccount(this.selectedPaymentMethod) ? 0 : calculateCreditCardFee(this.amount);
        this.totalAmount = +(this.amount + this.feeAmount).toFixed(2);

        if (!amountContainsMaxOneDotFollowedBy2decimals(this.amount)) {
            showSpanError("Max 2 decimal places allowed.", this.amountInputError!, this.amountInput!);
        } else if (this.amount < 5) {
            showSpanError("Must be at least 5 USD.", this.amountInputError!, this.amountInput!)
        } else if (this.amount > 5000) {
            showSpanError("Must be 5,000 USD or less.", this.amountInputError!, this.amountInput!)
        } else {
            hideSpanError(this.amountInputError!, this.amountInput!)
        }

        this.amountInput!.value = numberWithCommas(this.amountInput!.value);
    }

    private resetFields() {
        this.clearInputFields();
        this.errors = [];
    }

    private clearInputFields() {
        this.feeAmount= 0.00;
        this.totalAmount = 0.00;
        this.amount = 0.00;
        this.amountInput!.value = "";
    }

    private handleError(e: any) {
        if (e instanceof BridgeError) {
            this.errors = [e.userFriendlyMessage];
        } else {
            this.errors = [e.message];
        }
        this.showModalView(this.paymentDeclinedView)
    }

    private async submitDeposit() {
        this.showModalView(this.processingView, this.spinnerTitle);

        this.errors = validateContribution(this.amount, this.selectedPaymentMethod!.id,
            this.selectedPaymentMethod!.fundsTransferType, this.feeAmount);
        if(this.errors.length > 0) {
            this.showModalView(this.paymentDeclinedView);
            return false;
        }

        // check if instant settlement has enough balance
        try {
            const res: AxiosResponse<IstaBalanceCheck> = await this.bridge!.utilsApiService.getIstaBalanceCheck(this.amount);
            if (!res.data.usdAvailable) {
                this.errors = ["Currently max " + res.data.availableAmount + " USD can be deposited."];
                this.showModalView(this.paymentDeclinedView);
                return;
            }
        } catch (e) {
            this.handleError(e);
            return;
        }

        try {
            const res = await this.bridge!.contributionsApiService.createContribution(this.amount,
                this.selectedPaymentMethod!.id, this.selectedPaymentMethod!.fundsTransferType, this.feeAmount);

            log("Contribution: ", res);
            document.dispatchEvent(new CustomEvent('bri.deposit', {
                detail: {
                    amount: this.amount,
                    fundsTransferType: this.selectedPaymentMethod!.fundsTransferType
                }
            }));
            this.showModalView(this.paymentSuccessView);
        } catch (e) {
            if (e instanceof BridgeError) {
                if (extractDetailMessageFromError(e.externalError) == "insufficient funds")
                    this.errors = ["You have insufficient funds."];
                else
                    this.errors = [e.userFriendlyMessage];
            }
            else {
                this.errors = [extractMessageFromError(e)];
            }

            this.showModalView(this.paymentDeclinedView);
        }
    }

    private showConfirmSubmit(e: Event) {
        e.preventDefault();
        if (!amountContainsMaxOneDotFollowedBy2decimals(this.amount)) {
            showSpanError("Max 2 decimal places allowed.", this.amountInputError!, this.amountInput!);
            return false;
        } else if (this.amount < 5) {
            showSpanError("Must be at least 5 USD.", this.amountInputError!, this.amountInput!)
            return false;
        } else if (this.amount > 5000) {
            showSpanError("Must be 5,000 USD or less.", this.amountInputError!, this.amountInput!)
            return false;
        } else {
            hideSpanError(this.amountInputError!, this.amountInput!)
        }
        this.showModalView(this.depositConfirmationView);
        return false;
    }


    render() {
        return html`
            <div class="widget" id="deposit-amount" hidden>
              <!-- Navigation -->
              <div class="grid">
                <div class="grid-cell">
                  <a @click="${this.backToPaymentMethods}" class="back">Back</a>
                </div>
                <div class="grid-cell">
                  <p class="label-upper text-center">Deposit</p>
                </div>
                <div class="grid-cell">
            
                </div>
              </div>
            
              <form class="margin-top-25" @submit=${(e:Event) => this.showConfirmSubmit(e)}>
                <div class="grid">
                  <div class="grid-cell">
                    <label for="deposit-amount-input">Deposit amount</label>
                  </div>
                  <div class="grid-cell">
                    <p class="label-small text-right">
                    <a @click="${this.backToPaymentMethods}">${extractPaymentMethodName(this.selectedPaymentMethod)} ending in ${this.selectedPaymentMethod?.last4Digits}</a></p>
                  </div>
                </div>
                <select name="currency-selector" class="input-currency-selector" disabled="">
                  <option value="usd" selected="selected">USD</option>
                </select>
                <input required class="margin-top-15 margin-bottom-15 text-center" value="" @keyup=${(e:InputEvent) => this.amountChange(e)} 
                type="text" id="deposit-amount-input" name="deposit-amount-input" placeholder="0"><br>
                <span class="red-error-text" style="margin-top: -10px" id="amount-error" hidden></span>
                
                <div class="grid">
                  <div class="grid-cell">
                    <p class="label-small margin-top-10 margin-bottom-25 text-left">Max: 5,000 USD</p>
                  </div>
                  <div class="grid-cell">
                    <p class="label-small margin-top-10 margin-bottom-25 text-right">Balance: ${this.userBalance.toFixed(2)} USD</p>
                  </div>
                </div>
            
                <!-- Amount summary -->
                <table class="fee-list">
                  <tbody>
                    <tr>
                      <td class="label-small">Fee</td>
                      <td class="label-small text-right">${this.feeAmount.toFixed(2)} USD</td>
                    </tr>
                    <tr class="border-top">
                      <td>Total</td>
                      <td class="text-right">${this.totalAmount.toFixed(2)} USD</td>
                    </tr>
                  </tbody>
                </table>
            
                <p class="text-center margin-top-15 margin-bottom-15"><input type="submit" value="Deposit" class="button"></p>
              </form> 
            </div>
            
            
            
            <div class="widget" id="deposit-confirmation" hidden>
              <div class="grid grid-center inherit-height">
                <div class="grid-cell">
                  <img src=${dollarGreenSvg} class="feature-icon">
                  <h2>Deposit ${this.amount.toFixed(2)} USD?</h2>
                  <div class="grid margin-top-15 padding-bottom-15 border-bottom">
                    <div class="grid-cell">
                      <p class="text-center label-small">From</p>
                      <p class="text-center text-bold">${extractPaymentMethodName(this.selectedPaymentMethod)} ending in ${this.selectedPaymentMethod.last4Digits}</p>
                    </div>
                  </div>
                  <div class="grid margin-top-25 margin-bottom-25">
                    <div class="grid-cell border-right">
                      <p class="text-center label-small">Before</p>
                      <p class="text-center text-bold">${this.userBalance.toFixed(2)} USD</p>
                    </div>
                    <div class="grid-cell">
                      <p class="text-center label-small">After</p>
                      <p class="text-center text-bold">${(this.userBalance + this.amount).toFixed(2)} USD</p>
                    </div>
                  </div>
                  
                  <div class="grid-cell" ?hidden="${isBankAccount(this.selectedPaymentMethod)}">
                     <p class="text-center label-small">You'll be charged a ${this.feeAmount.toFixed(2)} USD fee.</p>
                  </div>
                  
                  <div class="grid grid-center">
                    <div class="grid-cell">
                      <p class="text-center margin-top-15 margin-bottom-15"><a @click="${() => this.showModalView(this.depositAmountView)}">Cancel</a></p>
                    </div>
                    <div class="grid-cell">
                      <p class="margin-top-15 margin-bottom-15"><a @click="${this.submitDeposit}" class="button">Confirm</a></p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
                  
                  
              <div class="widget" id="processing-payment" hidden>
                  <div class="grid grid-center inherit-height">
                    <div class="grid-cell">
                      <img src=${dollarGreenSvg} class="feature-icon">
                      <h2 class="margin-bottom-10">Processing your payment</h2>
                      <p class="text-center margin-top-15 margin-bottom-10">This may take a few moments.</p>
                
                      <!-- Loading animation -->
                      <div class="loading-animation">
                        <label></label>
                        <label></label>
                        <label></label>
                        <label></label>
                        <label></label>
                        <label></label>
                      </div>
                    </div>
                  </div>
                </div>
                
                <!-- Payment declined -->
                <div class="widget" id="payment-declined" hidden>
                  <div class="grid grid-center inherit-height">
                    <div class="grid-cell">
                        <img src=${paymentDeclinedSvg} class="feature-icon">
                        <h2>Payment declined</h2>
                        ${this.errors.map((error: string) => html `
                        <p class="text-center">${error}</p>
                        `)}
                        <p class="text-center margin-top-15 margin-bottom-15">
                         <a @click="${() => this.showModalView(this.depositAmountView)}" class="button">${this.errors.length > 0 && this.errors[0].includes("insufficient") ? "Change amount": "Back"}</a>
                        </p>
                    </div>
                  </div>
                </div>
                
                <!-- Payment successful -->
                <div class="widget" id="payment-successful" hidden>
                  <div class="grid grid-center inherit-height">
                    <div class="grid-cell">
                      <img src=${paymentSuccessSvg} class="feature-icon">
                      <h2>Payment approved</h2>
                      <p class="text-center margin-top-5">${this.amount} USD has been added to your account in the form of USDb (Bridge Dollars).</p>
                      <p class="text-center margin-top-15 margin-bottom-15"><a @click="${this.backToHomeView}" class="button">Close</a></p>
                    </div>
                  </div>
                </div>
        `}

}

declare global {
    interface HTMLElementTagNameMap {
        'deposit-elem': DepositElem;
    }
}
