import {PrimaryAddressUS} from "./PrimaryAddressUS";
import {PrimaryPhoneNumber} from "./PrimaryPhoneNumber";

export class PatchContact {
    email?: string;
    name?: string;
    primaryAddress?: PrimaryAddressUS;
    primaryPhoneNumber?: PrimaryPhoneNumber;
    contactType: string = "natural_person"; // *required
    // Date (before today) e.g. "1980-06-09" -> yy-mm-dd
    dateOfBirth?: string;
    // enum "male", "female" or "other"
    sex?: string | undefined;
    taxCountry?: string;
    // US Tax ID (SSN or EIN) regex = ^\d{9}$
    taxIdNumber?: string;
    // US State Codes check
    taxState?: string;

    constructor(email?: string, name?: string, primaryAddress?: PrimaryAddressUS, primaryPhoneNumber?: PrimaryPhoneNumber
                , dateOfBirth?: string, sex?: string, taxCountry?: string,
                taxIdNumber?: string, taxState?: string) {
        this.email = email;
        this.name = name;
        this.primaryAddress = primaryAddress;
        this.primaryPhoneNumber = primaryPhoneNumber;
        this.dateOfBirth = dateOfBirth;
        this.sex = sex;
        this.taxCountry = taxCountry;
        this.taxIdNumber = taxIdNumber;
        this.taxState = taxState;
    }
}
