import {customElement, html, LitElement, property} from "lit-element";


// @ts-ignore
import mainCSS from "../../assets/css/main.css";
import {BridgeService} from "../../lib/BridgeService";
import {User} from "../../lib/models/User/User";
import {hideModalView} from "../logic/view-transitions";

// @ts-ignore
import withdrawalSuccessSvg from "../../assets/img/icon/payment-successful.svg";
// @ts-ignore
import withdrawalDeclinedSvg from "../../assets/img/icon/payment-declined.svg";
// @ts-ignore
import dollarOutGreenSvg from "../../assets/img/icon/dollar-out-green.svg";
// @ts-ignore
import checkEmailSvg from "../../assets/img/icon/check-email.svg";

import {AxiosResponse} from "axios";
import {WithdrawalRequest} from "../../lib/models/Transaction/withdrawal-request";
import {BankAccount} from "../../lib/models/bankAccount/BankAccount";
import {BridgeError} from "../../lib/models/errors/bridgeError";
import {
    extractDetailMessageFromError,
    extractMessageFromError,
    log, numberWithCommas
} from "../../lib/common/Utils";
import {hideSpanError, showSpanError} from "../logic/utils";
import {amountContainsMaxOneDotFollowedBy2decimals} from "../../lib/validation/validation";

@customElement('withdraw-elem')
class WithdrawElem extends LitElement {

    @property() bridge: BridgeService | undefined;
    @property() user: User | undefined;
    @property() userBalance: number = 0;

    @property() private activeView: HTMLElement | null | undefined;
    @property() private withdrawAmountView: HTMLElement | null | undefined;
    @property() private withdrawConfirmationView: HTMLElement | null | undefined;
    @property() private withdrawProcessingView: HTMLElement | null | undefined;
    @property() private withdrawalSuccessView: HTMLElement | null | undefined;
    @property() private withdrawalDeclinedView: HTMLElement | null | undefined;

    @property() private amountInput: HTMLInputElement | null | undefined;
    @property() private amountInputError: HTMLElement | undefined | null;


    @property() spinnerTitle: string = "Processing your withdrawal...";


    @property() private selectedPaymentMethod: BankAccount = new BankAccount("","","","","","","","");

    @property() private amount: number = 0.00;

    @property() private errors: string[] = [];

    constructor() {
        super();
    }

    static styles = [
        mainCSS
    ];

    protected firstUpdated(_changedProperties: any): void {
        super.firstUpdated(_changedProperties);

        this.withdrawAmountView = this.shadowRoot!.getElementById("deposit-amount");
        this.withdrawConfirmationView = this.shadowRoot!.getElementById("withdraw-confirmation");
        this.amountInput = <HTMLInputElement>this.shadowRoot!.getElementById("deposit-amount-input");
        this.amountInputError = this.shadowRoot!.getElementById("amount-error");

        this.withdrawalSuccessView = this.shadowRoot!.getElementById("withdrawal-successful");
        this.withdrawalDeclinedView = this.shadowRoot!.getElementById("withdraw-declined");

        this.withdrawProcessingView = this.shadowRoot!.getElementById("withdrawing-from-bridge");


        this.showModalView(this.withdrawAmountView);
    }

    private showModalView(modalView: HTMLElement | null | undefined, title?: string) {
        hideModalView(this.activeView)
        if(title) {
            this.spinnerTitle = title;
        }
        modalView!.style.display = "block";
        this.setActiveModalView(modalView);
    }

    private setActiveModalView(newActiveModal: HTMLElement | null | undefined) {
        this.activeView = newActiveModal;
    }

    private amountChange(event: InputEvent) {
        // skip for arrow keys
        if (event.which >= 37 && event.which <= 40){
            return;
        }

        this.amountInput!.value = this.amountInput!.value.split(",").join("");

        this.amount = +this.amountInput!.value;
        if (this.amount < 0) {
            this.resetFields();
        }

        if (!amountContainsMaxOneDotFollowedBy2decimals(this.amount)) {
            showSpanError("Max 2 decimal places allowed.", this.amountInputError!, this.amountInput!);
        } else if (this.amount < 1) {
            showSpanError("Must be at least 1 USD.", this.amountInputError!, this.amountInput!);
        } else if (this.amount > 5000) {
            showSpanError("Must be 5,000 USD or less.", this.amountInputError!, this.amountInput!);
        } else if (this.amount > this.userBalance) {
            showSpanError("Must be equal or less than balance.", this.amountInputError!, this.amountInput!);
        } else {
            hideSpanError(this.amountInputError!, this.amountInput!)
        }

        this.amountInput!.value = numberWithCommas(this.amountInput!.value);
    }

    private resetFields() {
        this.amount = 0.00;
        this.amountInput!.value = "";
        this.errors = [];
    }

    backToPaymentMethods() {
        this.resetFields();
        this.dispatchEvent(new CustomEvent('backToPaymentMethods', {}));
        // reset view
        this.showModalView(this.withdrawAmountView);
    }

    private backToHomeView() {
        this.resetFields();
        this.dispatchEvent(new CustomEvent('backToHomeView', {}));
        // reset view
        this.showModalView(this.withdrawAmountView);
    }

    private showConfirmWithdraw(e: Event) {
        e.preventDefault();

        if (!amountContainsMaxOneDotFollowedBy2decimals(this.amount)) {
            showSpanError("Max 2 decimal places allowed.", this.amountInputError!, this.amountInput!);
            return false;
        } else if (this.amount < 1) {
            showSpanError("Must be at least 1 USD.", this.amountInputError!, this.amountInput!);
            return false;
        } else if (this.amount > 5000) {
            showSpanError("Must be 5,000 USD or less.", this.amountInputError!, this.amountInput!);
            return false;
        } else if (this.amount > this.userBalance) {
            showSpanError("Must be equal or less than balance.", this.amountInputError!, this.amountInput!);
            return false;
        } else {
            hideSpanError(this.amountInputError!, this.amountInput!)
        }

        this.showModalView(this.withdrawConfirmationView);
        return false;
    }

    private async submitWithdrawal() {
        this.showModalView(this.withdrawProcessingView, this.spinnerTitle);
        // build transaction
        let withdrawalRequestTx = this.bridge!.buildWithdrawalRequestTransaction(this.user!.iconWalletAddress!,this.amount)
        // signature of transaction
        withdrawalRequestTx.signature = await this.bridge!.signTransaction(withdrawalRequestTx);
        log("withdrawalRequestTx: ", withdrawalRequestTx);

        const withdrawalRequest: WithdrawalRequest = new WithdrawalRequest(withdrawalRequestTx, this.selectedPaymentMethod.id);


        await this.bridge!.withdrawApiService.requestWithdrawal(withdrawalRequest)
            .then((res:AxiosResponse) => {
                log("Withdrawal: ", res);
                document.dispatchEvent(new CustomEvent('bri.withdraw', {
                    detail: {
                        amount: this.amount,
                    }
                }));
                this.showModalView(this.withdrawalSuccessView);
            })
            .catch((e:any) => {
                if (e instanceof BridgeError) {
                    if (extractDetailMessageFromError(e.externalError) == "insufficient funds")
                        this.errors = ["You have insufficient funds"];
                    else
                        this.errors = [e.userFriendlyMessage];
                } else {
                    this.errors = [extractMessageFromError(e)];
                }
                this.showModalView(this.withdrawalDeclinedView);
            });

        return false;
    }

    render() {
        return html`
            
            <div class="widget" id="deposit-amount" hidden>
              <!-- Navigation -->
              <div class="grid">
                <div class="grid-cell">
                  <a @click="${this.backToPaymentMethods}" class="back">Back</a>
                </div>
                <div class="grid-cell">
                  <p class="label-upper text-center">Withdraw</p>
                </div>
                <div class="grid-cell">
            
                </div>
              </div>
            
              <form class="margin-top-25" @submit=${(e:Event) => this.showConfirmWithdraw(e)}>
                <div class="grid">
                  <div class="grid-cell">
                    <label for="deposit-amount-input">Withdraw amount</label>
                  </div>
                  <div class="grid-cell">
                    <p class="label-small text-right">
                    <a @click="${this.backToPaymentMethods}">Bank ending in ${this.selectedPaymentMethod?.last4Digits}</a></p>
                  </div>
                </div>
                <select name="currency-selector" class="input-currency-selector" disabled="">
                  <option value="USDb" selected="selected">USDb</option>
                </select>
                <input required class="margin-top-15 margin-bottom-15 text-center" value="" @keyup=${(e:InputEvent) => this.amountChange(e)} 
                type="text" id="deposit-amount-input" name="deposit-amount-input" placeholder="0"><br>
                <span class="red-error-text" style="margin-top: -10px" id="amount-error" hidden></span>

                
                <div class="grid">
                  <div class="grid-cell">
                    <p class="label-small margin-top-10 margin-bottom-25 text-left">Max: 5,000 USDb</p>
                  </div>
                  <div class="grid-cell">
                    <p class="label-small margin-top-10 margin-bottom-25 text-right">Balance: ${this.userBalance.toFixed(2)} USDb</p>
                  </div>
                </div>
            
                <!-- Amount summary -->
                <table class="fee-list">
                  <tbody>
                    <tr>
                      <td class="label-small">Fee</td>
                      <td class="label-small text-right">0.00 USDb</td>
                    </tr>
                    <tr class="border-top">
                      <td>Total</td>
                      <td class="text-right">${this.amount.toFixed(2)} USDb</td>
                    </tr>
                  </tbody>
                </table>
            
                <p class="text-center margin-top-15 margin-bottom-15"><input type="submit" value="Withdraw" class="button"></p>
              </form> 
            </div>
            
            
            <div class="widget" id="withdraw-confirmation" hidden>
              <div class="grid grid-center inherit-height">
                <div class="grid-cell">
                  <img src=${dollarOutGreenSvg} class="feature-icon">
                  <h2>Withdraw ${this.amount.toFixed(2)} USDb?</h2>
                  <div class="grid margin-top-15 padding-bottom-15 border-bottom">
                    <div class="grid-cell">
                      <p class="text-center label-small">To</p>
                      <p class="text-center text-bold">Bank ending in ${this.selectedPaymentMethod.last4Digits}</p>
                    </div>
                  </div>
                  <div class="grid margin-top-25 margin-bottom-25">
                    <div class="grid-cell border-right">
                      <p class="text-center label-small">Before</p>
                      <p class="text-center text-bold">${this.userBalance.toFixed(2)} USDb</p>
                    </div>
                    <div class="grid-cell">
                      <p class="text-center label-small">After</p>
                      <p class="text-center text-bold">${(this.userBalance - this.amount).toFixed(2)} USDb</p>
                    </div>
                  </div>
                  <div class="grid grid-center">
                    <div class="grid-cell">
                      <p class="text-center margin-top-15 margin-bottom-15"><a @click="${() => this.showModalView(this.withdrawAmountView)}">Cancel</a></p>
                    </div>
                    <div class="grid-cell">
                      <p class="margin-top-15 margin-bottom-15"><a @click="${this.submitWithdrawal}" class="button">Confirm</a></p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            

              <!-- Withdrawal processing -->
              <div class="widget" id="withdrawing-from-bridge" hidden>
                  <div class="grid grid-center inherit-height">
                    <div class="grid-cell">
                      <img src="${dollarOutGreenSvg}" class="feature-icon">
                      <h2 class="margin-bottom-10">Withdrawing from Bridge</h2>
                      <p class="text-center margin-top-15 margin-bottom-10">This may take a few moments.</p>
                
                
                      <!-- Loading animation -->
                      <div class="loading-animation">
                        <label></label>
                        <label></label>
                        <label></label>
                        <label></label>
                        <label></label>
                        <label></label>
                      </div>
                    </div>
                  </div>
                </div>
                
                <!-- Send email to confirm withdrawal view -->
                <div class="widget" id="check-email" hidden>
                  <div class="grid grid-center inherit-height">
                    <div class="grid-cell">
                      <img src=${checkEmailSvg} class="feature-icon">
                      <h2 class="margin-bottom-10">Check your email</h2>
                      <p class="text-center margin-bottom-15">To complete the withdrawal, click the link in the email sent to <span class="text-bold">${this.user?.email}</span>.</p>
                      <p class="label-small text-center">Don't see it? <a href="#">Resend email.</a></p>
                    </div>
                  </div>
                </div>
                
                <!-- Withdrawal successful -->
                <div class="widget" id="withdrawal-successful" hidden>
                <!-- Navigation -->
                <div class="grid">
                  <div class="grid-cell">
                      <a @click="${this.backToHomeView}" class="back">Back</a>
                      </div>
                      <div class="grid-cell">
                        <p class="label-upper text-center">Withdraw</p>
                      </div>
                      <div class="grid-cell">
                  </div>
                  
                </div>
                  <div class="grid grid-center inherit-height">
                    <div class="grid-cell">
                      <img src=${withdrawalSuccessSvg} class="feature-icon">
                      <h2>Withdrawal complete</h2>
                      <p class="text-center margin-top-5">${this.amount.toFixed(2)} USD has been sent to your bank account.</p>
                    </div>
                  </div>
                </div>
                
                <!-- Withdrawal declined -->
                <div class="widget" id="withdraw-declined" hidden>
                  <div class="grid grid-center inherit-height">
                    <div class="grid-cell">
                      <img src=${withdrawalDeclinedSvg} class="feature-icon">
                      <h2>Withdrawal failed</h2>
                        ${this.errors.map((error: string) => html `
                        <p class="text-center margin-top-5">${error}</p>
                        `)}
                      <p class="text-center margin-top-15 margin-bottom-15"><a @click="${() => this.showModalView(this.withdrawAmountView)}" class="button">Try again</a></p>
                
                    </div>
                  </div>
                </div>
                
        `;}
}

declare global {
    interface HTMLElementTagNameMap {
        'withdraw-elem': WithdrawElem;
    }
}
