import {CodeLists} from "../common/code-lists";
import {amountContainsMaxOneDotFollowedBy2decimals} from "./validation";

export function validateContribution(amount: number, fundsTransferMethodId: string, fundsTransferType: string,
                                     fee: number = 0.00): string[] {
    let errors: string[] = [];

    if (!amount) {
        errors.push("Invalid/Empty amount");
    }
    else if(amount < 5) {
        errors.push("Amount must be greater than 5");
    }
    else {
        if (!amountContainsMaxOneDotFollowedBy2decimals(amount))
            errors.push("Invalid amount, use dot for decimals");
    }

    if (!fundsTransferMethodId) errors.push("Empty funds transfer method id");

    if (!fundsTransferType) errors.push("Empty funds transfer type");
    else if (!CodeLists.supportedDepositFundsTransferTypes.some(type => type == fundsTransferType))
        errors.push("Invalid funds transfer type, should be one of ['ach', 'credit_card']");

    if (fundsTransferType == "credit_card") {
        if (fee <= 0.00) errors.push("Fee for credit card must be greater than 0.00")
        else {
            const splitArr = String(fee).split(".")
            if (String(fee).includes(".") && splitArr[splitArr.length - 1].length > 2) {
                errors.push("Invalid fee, must have maximum 2 decimal places (e.g. 1.05)");
            }
        }
    }
    return errors;
}
