export class BankAccount {
    id: string;
    contactName: string;
    bankName: string;
    name: string;
    type: string;
    routingNumber: string;
    last4Digits: string;
    fundsTransferType: string;


    constructor(id: string, contactName: string, bankName: string, name: string, type: string, routingNumber: string,
                last4Digits: string, fundsTransferType: string) {
        this.id = id;
        this.contactName = contactName;
        this.bankName = bankName;
        this.name = name;
        this.type = type;
        this.routingNumber = routingNumber;
        this.last4Digits = last4Digits;
        this.fundsTransferType = fundsTransferType;
    }
}
