import {LitElement, html, customElement, property} from 'lit-element';
import {hideModalView} from "../logic/view-transitions"
import {Account} from "../../lib/models/PrimeTrust/Account";
import {CustodialAccountAttributes} from "../../lib/models/PrimeTrust/CustodialAccountAttributes";
import {NaturalPersonContactUS} from "../../lib/models/PrimeTrust/NaturalPersonContactUS";
import {PrimaryAddressUS} from "../../lib/models/PrimeTrust/PrimaryAddressUS";
import {PrimaryPhoneNumber} from "../../lib/models/PrimeTrust/PrimaryPhoneNumber";
import {
    validateAccount,
    validateDateString, validateFileExtension,
    validateName, validatePrimaryAddressRegionUS, validateStreet,
    validateTaxIdNumber, validateUsPostalCode
} from "../../lib/validation/account-validation";
import {AxiosResponse} from "axios";
import {BridgeService} from "../../lib/BridgeService";
import {User} from "../../lib/models/User/User";
import {CodeLists} from "../../lib/common/code-lists";
// import {enterPressed} from "../logic/utils";

// css styles
// @ts-ignore
import mainCSS from '../../assets/css/main.css';
// @ts-ignore
import kycUser from '../../assets/img/icon/kyc-user.svg';
// @ts-ignore
import processingSvg from "../../assets/img/icon/kyc-user-wait.svg";
// @ts-ignore
import errorSvg from "../../assets/img/icon/error.svg";

import {NaturalPersonContactNonUS} from "../../lib/models/PrimeTrust/NaturalPersonContactNonUS";

// child elements
import "./kyc-documents";
import {extractFileExtension, log} from "../../lib/common/Utils";
import {BridgeError} from "../../lib/models/errors/bridgeError";
import {KycDocument} from "../../lib/models/PrimeTrust/KycDocument";
import {StandardKycDocumentCheck} from "../../lib/models/PrimeTrust/StandardKycDocumentCheck";
import {StandardKycDocumentCheckOther} from "../../lib/models/PrimeTrust/StandardKycDocumentCheckOther";
import {hideSpanError, showSpanError} from "../logic/utils";
import {environment} from "../../environment/environment";


@customElement('kyc-account')
class KycAccount extends LitElement {

    @property() bridge: BridgeService | undefined;
    @property() user: User | undefined;

    // Create account elements
    @property() accountName: HTMLInputElement | null | undefined;
    @property() accountBirthDate: HTMLInputElement | null | undefined;
    @property() accountIdNumber: HTMLInputElement | null | undefined;
    @property() accountStreet: HTMLInputElement | null | undefined;
    @property() accountPostalCode : HTMLInputElement | any;
    @property() accountCity: HTMLInputElement | null | undefined;
    @property() accountRegionOrState: HTMLInputElement | null | undefined;
    @property() accountAddressCountry: HTMLInputElement | null | undefined;
    @property() accountCountry: HTMLInputElement | null | undefined;
    @property() accountPhoneNumber: string = "1231231231"; // mocked phone number as we do not require it

    private documentType : HTMLInputElement | undefined;
    private documentOther: HTMLInputElement | undefined;
    private documentFrontSide: HTMLInputElement | undefined;
    private documentBackSide: HTMLInputElement | undefined;

    @property() errors: string[] = [];
    // verify identity error spans
    @property() nameErrorSpan: HTMLElement | undefined | null;
    @property() dobErrorSpan: HTMLElement | undefined | null;
    @property() countryErrorSpan: HTMLElement | undefined | null;
    @property() idErrorSpan: HTMLElement | undefined | null;
    @property() photoIdErrorSpan: HTMLElement | undefined | null;
    @property() photoIdFrontErrorSpan: HTMLElement | undefined | null;
    @property() photoIdBackErrorSpan: HTMLElement | undefined | null;
    // verify address error spans
    @property() streetErrorSpan: HTMLElement | undefined | null;
    @property() cityErrorSpan: HTMLElement | undefined | null;
    @property() stateOrRegionErrorSpan: HTMLElement | undefined | null;
    @property() postcodeErrorSpan: HTMLElement | undefined | null;
    @property() addressCountryErrorSpan: HTMLElement | undefined | null;


    @property() selectedDocumentType: string = "drivers_license";
    @property() selectedAddressCountry: string = "US";
    @property() selectedCountry: string = "US";

// modal views
    @property() kycIntroModalView : HTMLElement | null | undefined;
    @property() kycIdentityModalView : HTMLElement | null | undefined;
    @property() kycAddressModalView : HTMLElement | null | undefined;
    @property() kycProcessingModalView : HTMLElement | null | undefined;
    @property() kycAgreementModalView: HTMLElement | null | undefined;
    @property() kycVerifyingView: HTMLElement | null | undefined;
    @property() beforeErrorView: HTMLElement | null | undefined;
    @property() errorView: HTMLElement | null | undefined;

    @property() accountAgreement: HTMLElement | null | undefined;

    @property() activeView: HTMLElement | null | undefined;

    constructor() {
        super();
    }

    static styles = [
        mainCSS
    ];

    protected firstUpdated(_changedProperties: any): void {
        super.firstUpdated(_changedProperties);
        this.kycIntroModalView = this.shadowRoot!.getElementById("kyc-intro");
        this.kycIdentityModalView = this.shadowRoot!.getElementById("verify-identity");
        this.kycAddressModalView = this.shadowRoot!.getElementById("kyc-address");
        this.kycProcessingModalView = this.shadowRoot!.getElementById("processing-details");
        this.kycAgreementModalView = this.shadowRoot!.getElementById("kyc-agreement");
        this.kycVerifyingView = this.shadowRoot!.getElementById("kyc-verifying")
        this.errorView = this.shadowRoot!.getElementById("error")

        // account elements
        this.accountName = <HTMLInputElement>this.shadowRoot!.getElementById("verify-name");
        this.accountBirthDate = <HTMLInputElement>this.shadowRoot!.getElementById("verify-dob");
        this.accountIdNumber = <HTMLInputElement>this.shadowRoot!.getElementById("verify-id-number");
        this.accountStreet = <HTMLInputElement>this.shadowRoot!.getElementById("verify-street");
        this.accountPostalCode = <HTMLInputElement>this.shadowRoot!.getElementById("verify-post-code");
        this.accountCity = <HTMLInputElement>this.shadowRoot!.getElementById("verify-city");
        this.accountRegionOrState = <HTMLInputElement>this.shadowRoot!.getElementById("verify-region");
        this.accountAddressCountry = <HTMLInputElement>this.shadowRoot!.getElementById("verify-address-country");
        this.accountCountry = <HTMLInputElement>this.shadowRoot!.getElementById("verify-country");

        this.documentFrontSide =  <HTMLInputElement>this.shadowRoot!.getElementById("verify-photo-id-front");
        this.documentBackSide =  <HTMLInputElement>this.shadowRoot!.getElementById("verify-photo-id-back");
        this.documentOther = <HTMLInputElement>this.shadowRoot!.getElementById("kyc-document-other");
        this.documentType =  <HTMLInputElement>this.shadowRoot!.getElementById("verify-photo-id");

        this.nameErrorSpan = this.shadowRoot!.getElementById("name-error");
        this.dobErrorSpan = this.shadowRoot!.getElementById("dob-error");
        this.idErrorSpan = this.shadowRoot!.getElementById("id-error");
        this.countryErrorSpan = this.shadowRoot!.getElementById("country-error");
        this.photoIdErrorSpan = this!.shadowRoot!.getElementById("photo-id-error");
        this.photoIdFrontErrorSpan = this!.shadowRoot!.getElementById("photo-id-front-error");
        this.photoIdBackErrorSpan = this!.shadowRoot!.getElementById("photo-id-back-error");

        this.streetErrorSpan = this!.shadowRoot!.getElementById("street-error");
        this.cityErrorSpan = this!.shadowRoot!.getElementById("city-error");
        this.stateOrRegionErrorSpan = this!.shadowRoot!.getElementById("state-or-region-error");
        this.postcodeErrorSpan = this!.shadowRoot!.getElementById("post-error");
        this.addressCountryErrorSpan = this!.shadowRoot!.getElementById("address-country-error");


        this.accountAgreement = this.shadowRoot!.getElementById("account-agreement");

        this.showModalView(this.kycIntroModalView);
        this.initMockAccountValues();
    }

    private async createAccountAgreementAndShowIt() {
        this.showModalView(this.kycProcessingModalView);
        try {
            const res: AxiosResponse = await this.bridge!.accountApiService.postAccountAgreementPreview(this.getAccountOwnerData());
            log("Account agreement preview:", res.data);
            this.accountAgreement!.innerHTML = res.data.data.attributes.content;
            this.showModalView!(this.kycAgreementModalView);
        } catch (e) {
            this.handleError(e);
        }
    }


    private async onCreateAccountSubmit(e: Event) {
        e.preventDefault();
        this.showModalView(this.kycProcessingModalView);

        try {
            // Do not try to create PrimeTrust account if already established
            if (!this.user?.primeTrustAccId) {
                // Establish PrimeTrust account. If error (validation) occurred then return
                if (!await this.createPrimeTrustAccount()) {
                    return false;
                }
            } else {
                log(`PrimeTrust account with id=${this.user.primeTrustAccId} already established`);
            }
            // Upload KYC documents and create checks
            await this.uploadKycDocuments();
        } catch (e) {
            this.handleError(e);
            return false;
        }
        this.showModalView(this.kycVerifyingView);

        return false;
    }

    private async uploadKycDocuments() {
        const documentsToUpload: File[] = [this.documentFrontSide!.files![0]];
        if (this.driverLicenseSelected()) documentsToUpload.push(this.documentBackSide!.files![0]);
        log("documentsToUpload:", documentsToUpload);

        // check that there are documents to be uploaded
        if (documentsToUpload.length <= 0) {
            throw new BridgeError("No documents to upload. Please select documents to upload and try again.");
        }

        let kycDocument: KycDocument;
        for (const [, file] of documentsToUpload.entries()) {
            kycDocument = new KycDocument(extractFileExtension(file));
            let res: AxiosResponse = await this.bridge!.kycApiService.postAccountDocument(kycDocument, file);
            log("Document uploaded:", file.name);
            const documentId = res.data.data.id;
            let kycDocumentCheck: StandardKycDocumentCheck;
            const docCountry = this.accountAddressCountry!.value;
            if (this.selectedDocumentType == "other") {
                kycDocumentCheck = new StandardKycDocumentCheckOther(documentId, docCountry, this.documentType!.value,
                    this.documentOther!.value, true, true, false);
            }
            else {
                kycDocumentCheck = new StandardKycDocumentCheck(documentId, docCountry, this.documentType!.value,
                    true,true,false);
            }
            res = await this.bridge!.kycApiService.postKycDocumentChecks(kycDocumentCheck);
            log("Kyc document check result:", res);
        }
    }


    private async createPrimeTrustAccount(): Promise<boolean> {
        const account = new Account(
            new CustodialAccountAttributes(
                "custodial_account_" + this.accountName!.value.trim(),
                this.getAccountOwnerData()
            )
        );

        log("Creating account:", account);
        // validate fields and log errors if it fails
        const errors = validateAccount(account, this.accountCountry?.value == "US", this.accountAddressCountry?.value == "US");
        if (errors.length != 0) {
            this.errors = errors;
            this.showModalView(this.errorView);
            return false;
        }

        // create account and get updated user
        const accountResponse: AxiosResponse = await this.bridge!.accountApiService.postAccount(account);
        log("Created Prime Trust account: ", accountResponse.data);
        // dispatch account creation event to main widget
        const userResponse: AxiosResponse<User> = await this.bridge!.userApiService.getUser();
        log("Updated user:", userResponse.data);

        // dispatch account creation event to main widget
        this.accountCreatedEvent(userResponse.data);
        return true;
    }

    initMockAccountValues() {
        // init mock values only if not in production
        if (!environment.production) {
            this.accountName!.value = "John Doe";
            this.accountBirthDate!.value = "1970-01-01";
            this.accountIdNumber!.value = "123123123";
            this.accountStreet!.value = "330 S. Rampart";
            this.accountPostalCode!.value = "89145";
            this.accountCity!.value = "Las Vegas";
            this.accountRegionOrState!.value = "NV";
            this.accountAddressCountry!.value = "US";
            this.accountCountry!.value = "US";
        }
    }

    private getAccountOwnerData(): NaturalPersonContactUS | NaturalPersonContactNonUS {
        if (this.accountCountry?.value == "US") {
            return new NaturalPersonContactUS (
                this.user!.email,
                this.accountName!.value.trim(),
                new PrimaryAddressUS(
                    this.accountCity!.value.trim(),
                    this.accountAddressCountry!.value,
                    this.accountPostalCode.value.trim(),
                    this.accountRegionOrState!.value,
                    this.accountStreet!.value.trim(),
                    undefined
                ),
                new PrimaryPhoneNumber(
                    this.accountCountry!.value,
                    this.accountPhoneNumber,
                    false
                ),
                this.accountBirthDate!.value.trim(),
                undefined,
                this.accountIdNumber!.value.trim(),
                this.accountRegionOrState!.value
            );
        } else {
            return new NaturalPersonContactNonUS(
                this.user!.email,
                this.accountName!.value.trim(),
                new PrimaryAddressUS(
                    this.accountCity!.value.trim(),
                    this.accountAddressCountry!.value,
                    this.accountPostalCode.value.trim(),
                    this.accountRegionOrState!.value,
                    this.accountStreet!.value.trim(),
                    undefined
                ),
                new PrimaryPhoneNumber(
                    this.accountCountry!.value,
                    this.accountPhoneNumber,
                    false
                ),
                this.accountBirthDate!.value.trim(),
                undefined,
                this.accountCountry!.value,
                this.accountIdNumber!.value.trim()
            )
        }

    }

    private handleError(e: any) {
        if (e instanceof BridgeError) {
            this.errors = [e.userFriendlyMessage];
        } else {
            this.errors = [e.message];
        }
        this.showModalView(this.errorView)
    }

    accountCreatedEvent(user: User){
        this.dispatchEvent(new CustomEvent('accountCreated', {detail: {
            user: user
        }}))
    }

    backToHomeViewEvent(){
        let event = new CustomEvent('backToHomeView', {
        });
        this.dispatchEvent(event);
        this.showModalView(this.kycIntroModalView);
    }

    private showModalView(modalView: HTMLElement | null | undefined) {
        if (this.activeView == this.kycProcessingModalView || this.activeView == this.kycVerifyingView) {
            this.beforeErrorView = this.kycAddressModalView;
        } else {
            this.beforeErrorView = this.activeView;
        }
        hideModalView(this.activeView);
        modalView!.style.display = "block";
        this.setActiveModalView(modalView);
    }

    private setActiveModalView(newActiveModal: HTMLElement | null | undefined) {
        this.activeView = newActiveModal;
        log("New kyc active modal:", newActiveModal);
    }


    private validateIdentityInputFields(): boolean {
        let errorOccurred = false;
        // Validate and color border red if invalid
        // *name
        if (!this.accountName!.value) {
            showSpanError("Enter your name", this.nameErrorSpan!, this.accountName!)
            errorOccurred = true;
        }
        else if(!validateName(this.accountName!.value)) {
            showSpanError("Enter a valid name", this.nameErrorSpan!, this.accountName!)
            errorOccurred = true;
        } else {
            hideSpanError(this.nameErrorSpan!, this.accountName!);
        }
        // *date-of-birth
        if (!this.accountBirthDate!.value) {
            showSpanError("Enter your date of birth", this.dobErrorSpan!, this.accountBirthDate!)
            errorOccurred = true;
        }
        else if (!validateDateString(this.accountBirthDate!.value)) {
            showSpanError("Enter a valid date of birth", this.dobErrorSpan!, this.accountBirthDate!)
            errorOccurred = true;
        } else {
            hideSpanError(this.dobErrorSpan!, this.accountBirthDate!);
        }
        // *tax-id-number
        if (this.UScountrySelected() && !this.accountIdNumber!.value) {
            showSpanError("Enter your tax ID number", this.idErrorSpan!, this.accountIdNumber!)
            errorOccurred = true;
        }
        else if (this.UScountrySelected() && !validateTaxIdNumber(this.accountIdNumber!.value)) {
            showSpanError("Enter a valid tax ID number", this.idErrorSpan!, this.accountIdNumber!)
            errorOccurred = true;
        } else if (!this.accountIdNumber!.value) {
            showSpanError("Enter your ID number", this.idErrorSpan!, this.accountIdNumber!)
            errorOccurred = true;
        } else {
            hideSpanError(this.idErrorSpan!, this.accountIdNumber!);
        }
        // *country
        if (!this.accountCountry!.value) {
            showSpanError("Choose your country from the list", this.countryErrorSpan!, this.accountCountry!)
            errorOccurred = true;
        } else {
            hideSpanError(this.countryErrorSpan!, this.accountCountry!);
        }
        // *photo ID
        if (!this.documentType!.value) {
            showSpanError("Choose an ID type from the list", this.photoIdErrorSpan!, this.documentType!)
            errorOccurred = true;
        } else {
            hideSpanError(this.photoIdErrorSpan!, this.documentType!);
        }
        // *photo ID front
        if (!this.documentFrontSide!.files![0]) {
            showSpanError(this.selectedDocumentType == "drivers_license" ? "Upload the front of your ID":
                "Upload your photo ID", this.photoIdFrontErrorSpan!, this.documentFrontSide!)
            errorOccurred = true;
        } else if (!validateFileExtension(this.documentFrontSide!.files![0].name)) {
            showSpanError("Invalid file extension", this.photoIdFrontErrorSpan!, this.documentFrontSide!)
            errorOccurred = true;
        } else {
            hideSpanError(this.photoIdFrontErrorSpan!, this.documentFrontSide!);
        }
        // *photo ID back
        if (this.selectedDocumentType == "drivers_license") {
            if (!this.documentBackSide!.files![0]) {
                showSpanError("Upload the back of your ID", this.photoIdBackErrorSpan!, this.documentBackSide!)
                errorOccurred = true;
            } else if (!validateFileExtension(this.documentBackSide!.files![0].name)) {
                showSpanError("Invalid file extension", this.photoIdBackErrorSpan!, this.documentBackSide!)
                errorOccurred = true;
            } else {
                hideSpanError(this.photoIdBackErrorSpan!, this.documentBackSide!);
            }
        }
        return !errorOccurred;
    }

    private validateAddressInputFields(): boolean {
        let errorOccurred = false;
        // *street
        if (!this.accountStreet!.value) {
            showSpanError("Enter your street address", this.streetErrorSpan!, this.accountStreet!)
            errorOccurred = true;
        } else if (!validateStreet(this.accountStreet!.value)) {
            showSpanError("Enter a valid street address", this.streetErrorSpan!, this.accountStreet!)
            errorOccurred = true;
        } else {
            hideSpanError(this.streetErrorSpan!, this.accountStreet!);
        }
        // *city
        if (!this.accountCity!.value) {
            showSpanError("Enter your city", this.cityErrorSpan!, this.accountCity!)
            errorOccurred = true;
        }else {
            hideSpanError(this.cityErrorSpan!, this.accountCity!);
        }
        // *state/region
        if (!this.accountRegionOrState!.value) {
            showSpanError("Enter your state/region", this.stateOrRegionErrorSpan!, this.accountRegionOrState!)
            errorOccurred = true;
        } else if (this.UScountryAddressSelected() && !validatePrimaryAddressRegionUS(this.accountRegionOrState!.value)) {
            showSpanError("Enter a valid state/region", this.stateOrRegionErrorSpan!, this.accountRegionOrState!)
            errorOccurred = true;
        } else {
            hideSpanError(this.stateOrRegionErrorSpan!, this.accountRegionOrState!);
        }
        // *postcode
        if (!this.accountPostalCode!.value) {
            showSpanError("Enter your postcode", this.postcodeErrorSpan!, this.accountPostalCode!)
            errorOccurred = true;
        } else if (this.UScountryAddressSelected() && !validateUsPostalCode(this.accountPostalCode!.value)) {
            showSpanError("Enter a valid postcode", this.postcodeErrorSpan!, this.accountPostalCode!)
            errorOccurred = true;
        } else {
            hideSpanError(this.postcodeErrorSpan!, this.accountPostalCode!);
        }
        // *country
        if (!this.accountAddressCountry!.value) {
            showSpanError("Choose your country from the list", this.addressCountryErrorSpan!, this.accountAddressCountry!)
            errorOccurred = true;
        } else {
            hideSpanError(this.addressCountryErrorSpan!, this.accountAddressCountry!);
        }
        return !errorOccurred;
    }

    submitKycIdentityForm(event: Event) {
        event.preventDefault();
        // if invalid fields than return back to the same view with red colored invalid fields and messages
        if (this.validateIdentityInputFields()) {
            this.showModalView!(this.kycAddressModalView);
        }
        return false;
    }

    submitKycAddressForm(event:Event) {
        event.preventDefault();
        // if invalid fields than return back to the same view with red colored invalid fields and messages
        if (this.validateAddressInputFields()) {
            this.createAccountAgreementAndShowIt();
        }
        return false;
    }

    private documentIdTypeChange() {
        this.selectedDocumentType = this.documentType!.value;
        log("Changed document type to: ", this.selectedDocumentType);
    }

    private UScountrySelected(): boolean {
        return this.selectedCountry == "US";
    }

    private UScountryAddressSelected(): boolean {
        return this.selectedAddressCountry == "US";
    }

    private addressCountryChange() {
        this.selectedAddressCountry = this.accountAddressCountry!.value;
    }

    private countryChange() {
        this.selectedCountry = this.accountCountry!.value;
    }

    private driverLicenseSelected(): boolean {
        return this.selectedDocumentType == "drivers_license";
    }

    render() {
        return html`
                <!-- KYC intro -->
                <div class="widget" id="kyc-intro" hidden>
                  <div class="grid grid-center inherit-height">
                    <div class="grid-cell">
                      <img src=${kycUser} class="feature-icon">
                      <h2>Verify your account</h2>
                      <p class="label-small text-center margin-top-15">To deposit and withdraw USD, you'll need to verify your identity and address.</p>
                      <p class="label-small text-center margin-top-15 margin-bottom-10">This will only take a few minutes.</p>
                      <div class="grid grid-center">
                        <div class="grid-cell">
                          <p class="text-center margin-top-15 margin-bottom-15"><a @click="${this.backToHomeViewEvent}">Not now</a></p>
                        </div>
                        <div class="grid-cell">
                          <p class="margin-top-15 margin-bottom-15"><a @click="${() => {
                            this.showModalView!(this.kycIdentityModalView)}}" class="button">Get started</a></p>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>

                
                <!-- Verify identity -->
                <div class="widget" id="verify-identity" hidden>
                  <!-- Navigation -->
                  <div class="grid grid-center">
                    <div class="grid-cell">
                      <a @click="${this.backToHomeViewEvent}" class="back">Back</a>
                    </div>
                    <div class="grid-cell">
                      <p class="label-upper text-center">Verify identity</p>
                    </div>
                    <div class="grid-cell">
                
                    </div>
                  </div>
                
                  <form class="margin-top-35 square-input" @submit=${(e:Event) => this.submitKycIdentityForm(e)}>
                    <div class="grid">
                      <div class="grid-cell margin-right-25">
                        <label for="verify-name">Name</label><br>
                        <input class="margin-top-10 margin-bottom-20" type="text" id="verify-name" name="verify-name" required autocomplete="on">
                        <span class="error-span" id="name-error" hidden></span>
                      </div>
                      <div class="grid-cell">
                        <label for="verify-dob">Date of birth</label><br>
                        <input class="margin-top-10 margin-bottom-20 text-center" type="text" id="verify-dob" name="verify-dob" placeholder="YYYY-MM-DD" required autocomplete="on">
                        <span class="error-span" id="dob-error" hidden></span>
                      </div>
                    </div>
                
                    <div class="grid">
                      <div class="grid-cell margin-right-25">
                        <label for="verify-country">Country</label><br>
                        <select name="verify-country" @change=${() => this.countryChange()} class="margin-top-10 margin-bottom-20" id="verify-country">
                          <optgroup>
                          ${CodeLists.countryCodes.map(code => html `<option value="${code.code}" label="${code.name}">${code.name}</option>`)}
                          </optgroup>
                        </select>
                        <span class="error-span" id="country-error" hidden></span>
                      </div>
                      <div class="grid-cell">
                        <label for="verify-id-number">${this.UScountrySelected() ? "ID number (SSN)":"ID number"}</label><br>
                        <input class="margin-top-10 margin-bottom-20" type="text" id="verify-id-number" name="verify-id-number" required autocomplete="on">
                        <span class="error-span" id="id-error" hidden></span>
                      </div>
                    </div>
                
                    <div class="grid">
                      <div class="grid-cell margin-right-25">
                        <label for="verify-photo-id">Photo ID</label><br> 
                        <select @change=${() => this.documentIdTypeChange()} name="verify-photo-id" class="margin-top-10 margin-bottom-20" id="verify-photo-id" required>
                            <optgroup>
                            ${Array.from(CodeLists.kycDocumentsTypesPrettyMap).map(([key, value]) => {
                                if (this.UScountrySelected() && "drivers_license" == key)
                                    return html `<option value="${key}" label="${value}" selected>${value}</option>`;
                                else
                                    return html `<option value="${key}" label="${value}">${value}</option>`;
                            })}
                            </optgroup>
                        </select>
                        <span class="error-span" id="photo-id-error" hidden></span>
                      </div>
                      <div class="grid-cell" ?hidden=${!(this.selectedDocumentType != "other")}>
                      
                      </div>
                      <div class="grid-cell" ?hidden=${this.selectedDocumentType != "other"}>
                            <label for="kyc-document-other"></label>Other ID type<br>
                            <input class="margin-top-10 margin-bottom-20 text-center" type="text" id="kyc-document-other"
                             name="kyc-document-other" ?required=${this.selectedDocumentType == "other"} autocomplete="on">
                      </div>
                    </div>
                    
                    <div class="grid margin-bottom-20">
                        <div class="grid-cell">
                            <label ?hidden="${!this.driverLicenseSelected()}" for="verify-photo-id-front">Front of ID</label><br ?hidden="${!this.driverLicenseSelected()}">
                            <input class="margin-top-10 text-center" type="file" id="verify-photo-id-front" name="verify-photo-id-front" required>
                            <span class="error-span margin-top-0" id="photo-id-front-error" hidden></span>
                        </div>
                    </div>
                    
                    <div class="grid">
                        <div class="grid-cell" ?hidden="${!this.driverLicenseSelected()}">
                        <label for="verify-photo-id-back">Back of ID</label><br>
                        <input class="margin-top-10 text-center" type="file" id="verify-photo-id-back" name="verify-photo-id-back" 
                        ?required=${this.driverLicenseSelected()}>
                        <span class="error-span margin-top-0" id="photo-id-back-error" hidden></span>
                        </div>
                    </div>
                
                    <p class="text-center margin-top-35 margin-bottom-35"><input type="submit" value="Next" class="button"></p>
                  </form> 
                </div>
                
                
                <div class="widget" id="kyc-address" hidden>
                  <!-- Navigation -->
                  <div class="grid">
                    <div class="grid-cell">
                      <a @click="${() => {this.showModalView!(this.kycIdentityModalView)}}" class="back">Back</a>
                    </div>
                    <div class="grid-cell">
                      <p class="label-upper text-center">Verify address</p>
                    </div>
                    <div class="grid-cell">
                
                    </div>
                  </div>
                
                  <!-- Verify address form -->
                  <form class="margin-top-25 square-input" @submit=${(e:Event) => this.submitKycAddressForm(e)}>
                    <label for="verify-street">Street</label><br>
                    <input class="margin-top-10 margin-bottom-20" type="text" id="verify-street" name="verify-street" required autocomplete="on">
                    <span class="error-span" id="street-error" hidden></span>
                    <div class="grid">
                      <div class="grid-cell margin-right-25">
                        <label for="verify-city">City</label><br>
                        <input class="margin-top-10 margin-bottom-20" type="text" id="verify-city" name="verify-city" required autocomplete="on">
                        <span class="error-span" id="city-error" hidden></span>
                      </div>
                      <div class="grid-cell">
                        <label for="verify-region">State/Region</label><br>
                        ${(this.UScountryAddressSelected()) ?
                        html `<select name="verify-region" class="margin-top-10 margin-bottom-20" id="verify-region" required>
                            ${CodeLists.stateCodesUS.map(code => html `<option value="${code}">${code}</option>`)}
                        </select>`: 
                        html `<input class="margin-top-10 margin-bottom-20" type="text" id="verify-region" name="verify-region" required autocomplete="on">`
                        }
                        <span class="error-span" id="state-or-region-error" hidden></span>
                      </div>
                    </div>
                
                    <div class="grid">
                      <div class="grid-cell margin-right-25">
                        <label for="verify-postcode">Postcode</label><br>
                        <input class="margin-top-10 margin-bottom-20" type="text" id="verify-post-code" name="verify-post-code" required autocomplete="on">
                        <span class="error-span" id="post-error" hidden></span>
                      </div>
                      <div class="grid-cell">
                        <label for="verify-address-country">Country</label><br>
                        <select name="verify-address-country" @change=${() => this.addressCountryChange()} class="margin-top-10 margin-bottom-20" id="verify-address-country">
                          ${CodeLists.countryCodes.map(code => {
                              if (this.accountCountry?.value == code.code)
                                  return html `<option value="${code.code}" label="${code.name}" selected>${code.name}</option>`;
                              else
                                  return html `<option value="${code.code}" label="${code.name}">${code.name}</option>`
                          })}
                        </select>
                        <span class="error-span" id="address-country-error" hidden></span>
                      </div>
                    </div>
                
                    <p class="text-center margin-top-25 margin-bottom-25">
                    <input type="submit" value="Submit" class="button">
                    </p>
                  </form>
                </div>          
                
                <!-- KYC Account agreement-->
                <div class="widget" id="kyc-agreement" hidden>
                    <div class="grid">
                        <div class="grid-cell">
                          <a @click="${() => {this.showModalView(this.kycAddressModalView)}}" class="back">Back</a>
                        </div>
                        <div class="grid-cell">
                          <p class="label-upper text-center">Account agreement</p>
                        </div>
                         <div class="grid-cell">
                   
                        </div>
                   </div>
                    
                    <form @submit=${(e:Event) => this.onCreateAccountSubmit(e)}>
                      <p class="label-small text-center">Bridge uses Prime Trust to process payments.<br> Read and agree to their terms and conditions<br>to continue</p>
                        <div class="account-agreement-container" id="account-agreement"></div>
                        <input type="checkbox" name="account-terms-checkbox" id="account-terms-checkbox" class="margin-top-5 margin-bottom-10" required> I agree to the terms and conditions </br>
                        <div class="text-center margin-top-10 margin-bottom-10">
                            <input type="submit" value="Submit" class="button">
                        </div>
                    </form>
                </div>
                
                
                
                <!-- KYC processing -->       
                <div class="widget" id="processing-details" hidden>
                  <div class="grid grid-center inherit-height">
                    <div class="grid-cell">
                      <img src=${processingSvg} class="feature-icon">
                      <h2 class="margin-bottom-10">Processing your details</h2>
                      <p class="text-center margin-top-15 margin-bottom-10">This may take a few moments.</p>
                
                      <!-- Loading animation -->
                      <div class="loading-animation">
                        <label></label>
                        <label></label>
                        <label></label>
                        <label></label>
                        <label></label>
                        <label></label>
                      </div>
                    </div>
                  </div>
                </div>
                
                
                <div class="widget" id="kyc-verifying" hidden>
                  <div class="grid grid-center inherit-height">
                    <div class="grid-cell">
                      <img src=${processingSvg} class="feature-icon">
                      <h2>Verifying your account</h2>
                      <p class="text-center margin-top-25">This may take a while.<br> We'll notify you as soon as it's complete.</p>
                      <p class="margin-top-25 margin-bottom-15 text-center"><a @click="${this.backToHomeViewEvent}" class="button">Finish</a></p>
                    </div>
                  </div>
                </div>
                
                <!-- Error view -->
                <div class="widget" id="error" hidden>
                    <div class="grid">
                      <div class="grid-cell">
                          <a @click="${() => this.showModalView(this.beforeErrorView!)}" class="back">Back</a>
                          </div>
                          <div class="grid-cell">
                          </div>
                          <div class="grid-cell">
                      </div>
                </div>
                <div class="grid grid-center inherit-height">
                  <div class="grid-cell">
                    <img src=${errorSvg} class="feature-icon">
                    <h2 class="margin-bottom-10">Error</h2>
                    ${this.errors.map((error: string) => html `
                     <p class="text-center margin-top-5">${error}</p>
                    `)}
                  </div>
                   </div>
                </div>
    `;
    }
}
declare global {
    interface HTMLElementTagNameMap {
        'kyc-account': KycAccount;
    }
}
