import {NaturalPersonContactUS} from "../models/PrimeTrust/NaturalPersonContactUS";
import {CompanyContactUS} from "../models/PrimeTrust/CompanyContactUS";
import {instanceOfNaturalPersonContactUS, log} from "../common/Utils";
import {Account} from "../models/PrimeTrust/Account";
import {CodeLists} from "../common/code-lists";

export function validateEmail(email: string | undefined): boolean {
    if (!email) return false;
    return /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/.test(email);
}

export function validateFileExtension(filename: string | undefined): boolean {
    log("validating extension for name: ", filename);
    if (!filename || !filename.includes(".")) return false;
    return /^\.?[a-z]{2,5}$/.test(<string>filename.split(".").pop());
}

export function validateName(name: string | undefined): boolean {
    if (!name) return false;
    return name.length <= 80;
}

export function validateContactType(contactType: string | undefined, owner: NaturalPersonContactUS | CompanyContactUS | undefined): boolean {
    if (!contactType || !owner) return false;
    if (instanceOfNaturalPersonContactUS(owner)) {
        return contactType == "natural_person";
    } else {
        return validateCompanyContactType(contactType);
    }
}

export function validateUsPostalCode(postalCode: string | undefined): boolean {
    if (!postalCode) return false;
    return /^[0-9]{5}(-[0-9]{4})?$/.test(postalCode);
}

export function validateStreet(street: string | undefined): boolean {
    if (!street) return false;
    return street.length < 100;
}

export function validateUSstateCode(stateCode: string | undefined): boolean {
    if (!stateCode) return false;
    // check if any of the code equals state code
    return CodeLists.stateCodesUS.some(code => code == stateCode);
}

export function validateTaxCountry(country: string | undefined): boolean {
    if (!country) return false;
    return CodeLists.countryCodes.some(type => type.code == country);
}

export function validatePrimaryAddressCountry(country: string | undefined): boolean {
    if (!country) return false;
    return CodeLists.countryCodes.some(type => type.code == country);
}

export function validatePrimaryAddressRegionUS(country: string | undefined): boolean {
    if (!country) return false;
    return CodeLists.stateCodesUS.some(type => type == country);
}

export function validatePhoneCountry(country: string | undefined): boolean {
    if (!country) return false;
    return CodeLists.countryCodes.some(type => type.code == country);
}

export function validatePhoneNumber(phoneNumber: string | undefined): boolean {
    if (!phoneNumber) return false;
    return phoneNumber.length <= 25;
}

export function validateDateString(dateString: string | undefined): boolean {
    if (!dateString) return false;

    var regEx = /^\d{4}-\d{2}-\d{2}$/;
    if(!dateString.match(regEx)) return false;  // Invalid format
    var d = new Date(dateString);
    var dNum = d.getTime();
    if(!dNum && dNum !== 0) return false; // NaN value, Invalid date

    if(!(d.toISOString().slice(0,10) === dateString)) return false;

    return d <= new Date();
}

export function validateSex(sex: string | undefined): boolean {
    if (!sex) return false;
    return CodeLists.sexes.some(sexCode => sexCode == sex);
}

export function validateTaxIdNumber(taxIdNumber: string | undefined): boolean {
    if (!taxIdNumber) return false;
    return /^\d{9}$/.test(taxIdNumber);
}

export function validateUuidV4(uuidV4: string | undefined): boolean {
    if (!uuidV4) return false;
    return /^[0-9a-fA-F]{8}-[0-9a-fA-F]{4}-[0-9a-fA-F]{4}-[0-9a-fA-F]{4}-[0-9a-fA-F]{12}$/.test(uuidV4);
}

export function validateKycDocumentType(kycDocumentType: string | undefined): boolean {
    if (!kycDocumentType) return false;
    return CodeLists.kycDocumentTypes.some(type => type == kycDocumentType);
}

export function validateKycOtherDocumentType(kycOtherDocumentType: string | undefined): boolean {
    if (!kycOtherDocumentType) return false;
    return kycOtherDocumentType == "other";
}
export function validateKycDocumentCountry(kycDocumentCountry: string | undefined): boolean {
    if (!kycDocumentCountry) return false;
    return CodeLists.countryCodes.some(type => type.code == kycDocumentCountry);
}


export function validateCompanyContactType(contactType: string | undefined): boolean {
    if (!contactType) return false;
    return CodeLists.companyContactTypes.some(type => type == contactType);
}

export function validateAccount(account: Account | undefined, contactHasUSTaxInfo: boolean, hasUSAddress: boolean): string[] {
    if (!account) return ["Account object undefined, null or empty"];

    let errors = [];
    // *type
    if (account.type != "accounts") {
        errors.push("account.type");
    }
    // *attributes
    if (account.attributes) {

        // account-type
        if(account.attributes.accountType != "custodial") {
            errors.push("account.attributes.accountType != custodial");
        }
        // *name
        if(!validateName(account.attributes.name)) {
            errors.push("account.attributes.name");
        }
        // *authorized-signature
        if(!account.attributes.authorizedSignature) {
            errors.push("account.attributes.authorizedSignature");
        }
        // *owner
        if (account.attributes.owner) {

            if (instanceOfNaturalPersonContactUS(account.attributes.owner)) {
                // *email
                if(!validateEmail(account.attributes.owner.email)) {
                    errors.push("account.attributes.email");
                }

                // *name
                if(!validateName(account.attributes.owner.name)) {
                    errors.push("account.attributes.name");
                }

                // sex
                if (account.attributes.owner.sex && !validateSex(account.attributes.owner.sex)) {
                    errors.push("account.attributes.owner.sex");
                }
                // *contact-type
                if (!validateContactType(account.attributes.owner.contactType, account.attributes.owner)) {
                    errors.push("account.attributes.owner.contactType");
                }
                // *date-of-birth
                if (!validateDateString(account.attributes.owner.dateOfBirth)) {
                    errors.push("account.attributes.owner.dateOfBirth");
                }
                // *tax-country
                if (!validateTaxCountry(account.attributes.owner.taxCountry)) {
                    errors.push("account.attributes.owner.taxCountry");
                }
                // *tax-id-number
                if (contactHasUSTaxInfo && !validateTaxIdNumber(account.attributes.owner.taxIdNumber)) {
                    errors.push("account.attributes.owner.taxIdNumber");
                }
                // *tax-state
                if (!validateUSstateCode(account.attributes.owner.taxState)) {
                    errors.push("account.attributes.owner.taxState");
                }
                // *primary-phone-number
                if (account.attributes.owner.primaryPhoneNumber) {
                    // * phone number
                    if (!validatePhoneNumber(account.attributes.owner.primaryPhoneNumber.phoneNumber)) {
                        errors.push("account.attributes.owner.primaryPhoneNumber.number");
                    }
                    // * phone country
                    if (!validatePhoneCountry(account.attributes.owner.primaryPhoneNumber.phoneCountry)) {
                        errors.push("account.attributes.owner.primaryPhoneNumber.country");
                    }
                } else {
                    errors.push("account.attributes.owner.primaryPhoneNumber");
                }
                // *primary address
                if (account.attributes.owner.primaryAddress) {
                    // *city
                    if (!account.attributes.owner.primaryAddress.city) {
                        errors.push("account.attributes.owner.primaryAddress.city");
                    }
                    // *country
                    if (!validatePrimaryAddressCountry(account.attributes.owner.primaryAddress.country)) {
                        errors.push("account.attributes.owner.primaryAddress.country");
                    }
                    // *region
                    if (hasUSAddress && !validatePrimaryAddressRegionUS(account.attributes.owner.primaryAddress.region)) {
                        errors.push("account.attributes.owner.primaryAddress.region");
                    }
                    // *postal code
                    if (hasUSAddress && !validateUsPostalCode(account.attributes.owner.primaryAddress.postalCode)) {
                        errors.push("account.attributes.owner.primaryAddress.postalCode");
                    }
                    // *street-1
                    if (!validateStreet(account.attributes.owner.primaryAddress.street1)) {
                        errors.push("account.attributes.owner.primaryAddress.street1")
                    }
                } else {
                    errors.push("account.attributes.owner.primaryAddress");
                }
            }
        } else {
            errors.push("account.attributes.owner");
        }
    } else {
        errors.push("account.attributes");
    }

    return errors;
}

