  private handleOnViewBlockchainAddressClick(fromDeposit?: boolean) {
    if (!fromDeposit) {
      this.onMenuTriggerClick();
    }
    var opts = {
      errorCorrectionLevel: 'H',
      type: 'image/jpeg',
      quality: 0.3,
      margin: 1,
      color: {
        dark:"#2e2e2e",
        light:"#f1f4f9"
      }
    }

    QRCode.toDataURL(this.user?.iconWalletAddress, opts, (err: any, url: any) => {
      if (err) {
        this.handleError(err);
      } else {
        // @ts-ignore
        this.qrCodeContainer!.src  = url;
        this.showModalView(this.viewAddressView);
      }
    })
  }

  // handle scrolling down on home page
  private scrollHeaderLogic(modalView: HTMLElement | null | undefined) {
    const scrollTop = modalView!.scrollTop;
    if (Math.abs(this.lastScrollPosition - scrollTop) < 5) return;
    this.lastScrollPosition = scrollTop;
    if (scrollTop > 30 && !this.floatingHeaderActive) {
      this.floatingHeader!.classList.add("active");
      this.floatingHeaderActive = true;
      modalView!.scrollBy(0, 20)
    } else if (scrollTop < 30 && this.floatingHeaderActive) {
      this.floatingHeader!.classList.remove("active");
      this.floatingHeaderActive = false;
      modalView!.scrollBy(0, -scrollTop)
    }
  }

  private showProcessingView(svg: any, title: string) {
    this.processingTitle = title;
    this.processingSvg = svg;
    this.showModalView(this.processingView);
  }

  private async onKycStatusClick() {
    this.onMenuTriggerClick();
    if (this.user?.primeTrustAccId) {
      this.showProcessingView(kycUserWaitSvg, "Loading KYC information");
      try {
        // if CIP and AML not checked, try to force approve them in sandbox
        if (!environment.production && !(this.userKycData?.cipCleared && this.userKycData.amlCleared)) {
          await this.bridge.utilsApiService.approveCipAndAml();
        }
        await this.loadUserKycData();
        log(this.kycChecks);
        // @ts-ignore
        this.kycStatusElemView.getKycExceptions();
        this.showModalView(this.kycStatusElemView);
      } catch (e) {
        this.handleError(e);
      }
    } else {
      this.showModalView(this.kycAccountElemView);
    }
  }

  private onDepositClick() {
    // if token other than USDb is selected then show copy address view
    if (this.selectedToken && this.userTokensMap.size > 1 && this.selectedToken.tag != SupportedTokens.USDb) {
      this.handleOnViewBlockchainAddressClick(true);
      return;
    }
    // if user does not have linked PrimeTrust account then show kyc flow
    if (!this.user?.primeTrustAccId) {
      this.showModalView(this.kycAccountElemView);
    } else {
        this.showPaymentMethods();
    }
  }

  private onAssetClick(token: Token) {
    this.selectedToken = token;
  }

  private onCopyAddressClick() {
    let textArea = document.createElement("textarea");

    // Place in top-left corner of screen regardless of scroll position.
    textArea.style.position = 'fixed';
    textArea.style.top = "0";
    textArea.style.left = "0";

    // Ensure it has a small width and height. Setting to 1px / 1em
    // doesn't work as this gives a negative w/h on some browsers.
    textArea.style.width = '2em';
    textArea.style.height = '2em';

    // We don't need padding, reducing the size if it does flash render.
    textArea.style.padding = "0";

    // Clean up any borders.
    textArea.style.border = 'none';
    textArea.style.outline = 'none';
    textArea.style.boxShadow = 'none';

    // Avoid flash of white box if rendered for any reason.
    textArea.style.background = 'transparent';
    textArea.value = this.user?.iconWalletAddress!;

    document.body.appendChild(textArea);
    textArea.focus();
    textArea.select();

    try {
      var successful = document.execCommand('copy');
      var msg = successful ? 'successful' : 'unsuccessful';
      log('Copying text command was ' + msg);
      // show notification
      this.showNotification("Address copied!", NotificationType.success)
    } catch (err) {
      log('Oops, unable to copy');
    }

    document.body.removeChild(textArea);
  }

  private showNotification(message: string, notificationType: NotificationType) {
    log("Showing notification message: ", message);
    this.notificationMessage = message;
    if (this.notification && !this.notification.classList.contains('opened')) {
      switch (notificationType) {
        case NotificationType.normal:
          if (this.notification.classList.contains('error')) this.notification.classList.remove('error');
          if (this.notification.classList.contains('success')) this.notification.classList.remove('success');
          break;
        case NotificationType.success:
          if (this.notification.classList.contains('error')) this.notification.classList.remove('error');
          if (!this.notification.classList.contains('success')) this.notification.classList.add('success');
          break;
        case NotificationType.error:
          if (!this.notification.classList.contains('error')) this.notification.classList.remove('error');
          if (this.notification.classList.contains('success')) this.notification.classList.add('success');
          break;
      }
      this.notification.classList.remove('closed');
      this.notification.classList.remove('visually-hidden');
      this.notification.classList.add('opened');
      // auto close notification after 5 seconds
      // setTimeout(() => this.closeNotification(), 5000);
    }
  }

  private closeNotification() {
    if (this.notification) {
      if (this.notification.classList.contains('opened')) {
        this.notification.classList.remove('opened');
        this.notification.classList.add('closed');
        this.notification.classList.add('visually-hidden');
      }
    }
  }

