  private getTransactionsHtml() {
    if (this.selectedToken) {
      if ((this.selectedToken.tag == SupportedTokens.USDb && this.transactions.bridgeTransactions.length == 0) ||
          (this.selectedToken.tag == SupportedTokens.ICX && this.transactions.icxTransactions.length == 0)) {
        return html `     <!-- If no transactions -->
          <p class="text-center label-small margin-top-35">No transactions yet.</p>`
      } else {
        if (this.selectedToken.tag == SupportedTokens.USDb) {
          return html `
          <!-- If transactions -->
          <table class="transaction-list">
            <tbody>
            ${this.transactions.bridgeTransactions.map((transaction: BridgeTransaction, index) => {
            if (index != 0 && transaction.dateString == this.transactions.bridgeTransactions[index -1].dateString) {
              return html `
                <tr @click="${() => this.onTransactionClick(transaction)}"
                class="${transactionTypeToClass(transaction)}">
                  <td>${getTransactionName(transaction)}</td>
                  <td class="text-bold">${transaction.type == TransactionType.SENT || transaction.type == TransactionType.WITHDRAW ? "-":"+"} ${transaction.amount.toFixed(2)} ${this.selectedToken?.tag}</td>
                </tr>
                `
            } else {
              return html `
                 <tr>
                  <td colspan="2" class="date padding-top-15">${transaction.dateString.split(" ").slice(0, -1).join(" ")}</td>
                </tr>
                <tr @click="${() => this.onTransactionClick(transaction)}"
                class="${transactionTypeToClass(transaction)}">
                  <td>${getTransactionName(transaction)}</td>
                  <td class="text-bold">${transaction.type == TransactionType.SENT || transaction.type == TransactionType.WITHDRAW ? "-":"+"} ${transaction.amount.toFixed(2)} ${this.selectedToken?.tag}</td>
                </tr>
                `
            }
          })}
            </tbody>
            </table>
          `
        }
        else if (this.selectedToken.tag == SupportedTokens.ICX) {
          return html `
          <!-- If transactions -->
          <table class="transaction-list">
            <tbody>
            ${this.transactions.icxTransactions.map((transaction: IcxTransaction, index) => {
            if (index != 0 && transaction.dateString == this.transactions.icxTransactions[index -1].dateString) {
              return html `
                <tr @click="${() => this.onTransactionClick(transaction)}"
                class="${tokenTransactionTypeToClass(transaction)}">
                  <td>${transaction.type.valueOf()}</td>
                  <td class="text-bold">${transaction.type == TransactionType.SENT? "-":"+"} ${transaction.amount.toFixed(2)} ${this.selectedToken?.tag}</td>
                </tr>
                `
            } else {
              return html `
                 <tr>
                  <td colspan="2" class="date padding-top-15">${transaction.dateString.split(" ").slice(0, -1).join(" ")}</td>
                </tr>
                <tr @click="${() => this.onTransactionClick(transaction)}"
                class="${tokenTransactionTypeToClass(transaction)}">
                  <td>${transaction.type.valueOf()}</td>
                  <td class="text-bold">${transaction.type == TransactionType.SENT ? "-":"+"} ${transaction.amount.toFixed(2)} ${this.selectedToken?.tag}</td>
                </tr>
                `
            }
          })}
            </tbody>
            </table>
          `
        }
      }
    }
  }

  private getTransactionDetailHtml() {
    let title = html``;
    if (this.selectedTransaction instanceof BridgeTransaction) {
      if (this.selectedTransaction?.type == TransactionType.DEPOSITED) {
        title = html`<div class="grid margin-top-15 padding-bottom-15 border-bottom">
                      <div class="grid-cell">
                        <p class="text-center margin-bottom-5">Deposited using <span class="text-bold">
                        ${(this.selectedTransaction?.depositTransaction) ? CodeLists.fundsTransferTypeToPretty.get(this.selectedTransaction.depositTransaction.fundsTransferType): "-"}
                        </span></p>
                      </div>
                    </div>`
      } else if (this.selectedTransaction?.type == TransactionType.WITHDRAW) {
        title = html `<div class="grid margin-top-15 padding-bottom-15 border-bottom">
                        <div class="grid-cell">
                          <p class="text-center margin-bottom-5">${this.selectedTransaction?.withdrawTransaction?.status == "pending" ? "Withdrawing" : "Withdrawn"} using <span class="text-bold">ACH</span></p>
                        </div>
                      </div>`;

      }
    }
    if (this.selectedTransaction?.type == TransactionType.SENT || this.selectedTransaction?.type == TransactionType.RECEIVED) {
      let blockchainAddress = "";
      if (this.selectedTransaction instanceof BridgeTransaction) {
        blockchainAddress = this.selectedTransaction?.iconTransaction ? this.selectedTransaction.iconTransaction.toAddr : "Nan";
      } else {
        blockchainAddress = this.selectedTransaction.toAddr;
      }
      title = html`<div class="grid margin-top-15 padding-bottom-15 border-bottom">
                    <div class="grid-cell">
                      <p class="text-center label-small">${this.selectedTransaction?.type == TransactionType.SENT ? "Sent to": "Received from"}</p>
                      <p class="text-center text-bold">
                      <span class="blockchain-address">${blockchainAddress}</span>
                      </p>
                    </div>
                  </div>`
    }
    return html`
       <div class="grid-cell">
        <h2>${this.selectedTransaction?.type == TransactionType.SENT || this.selectedTransaction?.type == TransactionType.WITHDRAW ? "-":"+"} ${this.selectedTransaction? this.selectedTransaction.amount.toFixed(2): "Nan"}<span class="currency label-small">${this.selectedToken?.tag}</span></h2>
        ${title}
        <p class="text-center label-small">${this.selectedTransaction ?  this.selectedTransaction.dateString + " " + this.selectedTransaction.date.toLocaleTimeString(): "Nan"}</p>
      </div>
    `
  }

