import {Irc2TokenTransaction} from "./Irc2TokenTransaction";
import {Deposit} from "./Deposit";
import {Withdrawal} from "./Withdrawal";

export class BridgeTransaction {
    dateString: string;
    date: Date;
    amount: number;
    iconTransaction: Irc2TokenTransaction | undefined;
    depositTransaction: Deposit | undefined;
    withdrawTransaction: Withdrawal | undefined;
    type: TransactionType;


    constructor(dateString: string, date: Date, amount: number, iconTransaction: Irc2TokenTransaction | undefined,
                depositTransaction: Deposit | undefined, type: TransactionType, withdrawTransaction: Withdrawal | undefined) {
        this.dateString = dateString;
        this.date = date;
        this.amount = amount;
        this.iconTransaction = iconTransaction;
        this.depositTransaction = depositTransaction;
        this.withdrawTransaction = withdrawTransaction;
        this.type = type;
    }
}

export enum TransactionType {
    SENT = "Sent",
    RECEIVED = "Received",
    DEPOSITED = "Deposited",
    WITHDRAW = "Withdrawn"
}
