import {customElement, html, LitElement, property} from "lit-element";

// @ts-ignore
import mainCSS from "../../assets/css/main.css";
import {BridgeService} from "../../lib/BridgeService";
import {environment} from "../../environment/environment";
import {User} from "../../lib/models/User/User";
import {hideModalView} from "../logic/view-transitions";
import {BankAccount} from "../../lib/models/bankAccount/BankAccount";

// @ts-ignore
import bankSvg from "../../assets/img/icon/bank.svg";
// @ts-ignore
import paymentOptionBankSvg from "../../assets/img/icon/bank.svg";
// @ts-ignore
import cardSvg from "../../assets/img/icon/card.svg";
// @ts-ignore
import paymentOptionCardSvg from "../../assets/img/icon/card.svg";

// @ts-ignore
import errorSvg from "../../assets/img/icon/error.svg";
// custom child elements
import "./withdraw-elem";
import "./deposit-elem";

import {BridgeError} from "../../lib/models/errors/bridgeError";
import {isBankAccount, log} from "../../lib/common/Utils";
import {AxiosResponse} from "axios";
import {CreditCardResourceResponse} from "../../lib/models/CreditCard/CreditCardResourceResponse";
import {CreditCard} from "../../lib/models/CreditCard/CreditCard";
import {mapFundsTransferMethodToCreditCard} from "../../lib/common/Mapping";
import {CodeLists} from "../../lib/common/code-lists";
import {UserAction} from "../../lib/models/Enums/UserAction";

let verificationCodeInterval: any;

@customElement('payment-methods-elem')
class PaymentMethodsElem extends LitElement {

    @property() bridge: BridgeService | undefined;
    @property() user: User | undefined;
    @property() userBalance: number | undefined;

    @property() paymentMethodsView: HTMLElement | null | undefined;
    @property() private depositElem: HTMLElement | null | undefined;
    @property() private withdrawElem: HTMLElement | null | undefined;
    @property() private paymentOptionsTypeView: HTMLElement | null | undefined;
    @property() private paymentRemoveView: HTMLElement | null | undefined;
    @property() private errorView: HTMLElement | null | undefined;
    @property() private previousView: HTMLElement | null | undefined;

    @property() private plaidLinkScriptLoaded: boolean;

    @property() private activeView: HTMLElement | null | undefined;
    @property() private error: string = "";

    @property() private linkedBankAccounts: BankAccount[] = [];
    @property() private linkedCreditCards: CreditCard[] = [];

    @property() private selectedPaymentMethod: BankAccount | CreditCard = new BankAccount("","","","","","","","");
    @property() private selectedAction: UserAction = UserAction.Deposit;

    @property() private creditCardWidget: HTMLElement | null | undefined;
    @property() private creditCardModalView: HTMLElement | null | undefined;
    @property() private processingRemoveView: HTMLElement | null | undefined;
    @property() private loadingBankAccountView : HTMLElement | null | undefined;
    @property() private creditCardWidgetReady: boolean = false;
    @property() private creditCardWidgetLoaded: boolean = false;
    @property() private activeCreditCardResourceId = "";

    constructor() {
        super();
        this.plaidLinkScriptLoaded = false;
    }

    static styles = [
        mainCSS
    ];

    protected firstUpdated(_changedProperties: any): void {
        super.firstUpdated(_changedProperties);

        this.paymentMethodsView = this.shadowRoot!.getElementById("payment-methods");
        this.paymentOptionsTypeView = this.shadowRoot!.getElementById("payment-options-type-view")
        this.depositElem = this.shadowRoot!.getElementById("deposit-elem");
        this.withdrawElem = this.shadowRoot!.getElementById("withdraw-elem");
        this.paymentRemoveView = this.shadowRoot!.getElementById("payment-remove");
        this.processingRemoveView = this.shadowRoot!.getElementById("processing-remove");
        this.loadingBankAccountView = this.shadowRoot!.getElementById("loading-bank-acc");
        this.errorView = this.shadowRoot!.getElementById("error");
        // Prime Trust credit card widget element
        this.creditCardWidget = this.shadowRoot!.getElementById("credit-card-target");
        this.creditCardModalView = this.shadowRoot!.getElementById("cc-widget-wrapper");

        this.showModalView(this.paymentMethodsView);
    }

    protected updated(_changedProperties: any): void {
        super.updated(_changedProperties);
    }

    connectedCallback() {
        super.connectedCallback();
        // Plaid link events
        document.addEventListener('pl.onSuccess', async (e:any) => this.bankAccountAddedHandler(e));
        document.addEventListener('pl.onError', async (e:any) => this.handleError(e.detail.error));
        document.addEventListener("pl.preOnSuccess", () => {this.showModalView(this.loadingBankAccountView)})
        document.addEventListener("pl.onEvent", (e:any) => this.handlePlaidLinkOnEvent(e));
        // PrimeTrust credit card widget events
        document.addEventListener('pt.bootstrap.ready', () => this.creditCardWidgetReadyHandler());
        document.addEventListener("pt.app.ready", () => this.creditCardUiLoadedHandler());
        document.addEventListener("pt.app.verified", (e:any) => this.creditCardAddedHandler(e));
    }

    disconnectedCallback() {
        document.removeEventListener('pl.onSuccess', async (e:any) => this.bankAccountAddedHandler(e));
        document.removeEventListener('pl.onError', async (e:any) => this.handleError(e));
        document.removeEventListener('pt.bootstrap.ready', () => this.creditCardWidgetReadyHandler);
        document.removeEventListener("pt.app.ready", () => this.creditCardUiLoadedHandler());
        document.removeEventListener("pt.app.verified", (e:any) => this.creditCardAddedHandler(e));

        super.disconnectedCallback();
    }

    private handlePlaidLinkOnEvent(e: any) {
        const eventName = e.detail.eventName;
        const metadata = e.detail.metadata;
        log("handlePlaidLinkOnEvent -> eventName:", eventName, "metadata:", metadata);
        if (eventName == "EXIT") {
            this.showModalView(this.previousView);
        }
    }

    private async bankAccountAddedHandler(e: any) {
        log("Event pl.onSuccess FIRED!");
        const bankAccount: BankAccount = e.detail.bankAccount;
        log("Added bank acount: ", bankAccount);
        this.linkedBankAccounts = [bankAccount, ...this.linkedBankAccounts];
        this.handleOnPaymentMethodClick(bankAccount);
    }

    private async creditCardAddedHandler(e: any) {
        // the user has successfully added their credit card information to the linked contact
        // e.detail is an object containing the keys: "contactId", "fundsTransferMethodId"
        log("pt.app.verified ", e);

        const res = await this.bridge!.accountApiService.getFundsTransferMethodById(e.detail.fundsTransferMethodId);
        const creditCard: CreditCard = mapFundsTransferMethodToCreditCard(res.data.data);
        this.linkedCreditCards = [creditCard, ...this.linkedCreditCards];
        this.onCreditCardWidgetLeave();
        this.handleOnPaymentMethodClick(creditCard);
    }

    private creditCardWidgetReadyHandler() {
        log("Event pt.bootstrap.ready FIRED!");
        this.creditCardWidgetReady = true;
    }

    private creditCardUiLoadedHandler() {
        log("the credit card UI has been loaded and is ready to use!");
    }

    private showModalView(modalView: HTMLElement | null | undefined) {
        if (this.activeView != this.processingRemoveView && this.activeView != this.loadingBankAccountView
            && this.activeView != this.errorView) {
            this.previousView = this.activeView;
        }
        hideModalView(this.activeView);
        modalView!.style.display = "block";
        this.setActiveModalView(modalView);
    }

    private setActiveModalView(newActiveModal: HTMLElement | null | undefined) {
        this.activeView = newActiveModal;
    }

    private creditCardWidgetBack() {
        this.showModalView(this.paymentMethodsView);
        this.onCreditCardWidgetLeave();
    }

    private onCreditCardWidgetLeave() {
        // clear contents of credit card widget to avoid duplication
        this.creditCardWidget!.innerHTML = "";
        // clear interval
        clearInterval(verificationCodeInterval);
    }

    private handleError(e: any) {
        if (e instanceof BridgeError) {
            this.error = e.userFriendlyMessage;
        } else {
            this.error = e.message;
        }
        this.showModalView(this.errorView)
    }

    public showPreviousPaymentMethod() {
        let previousPaymentMethod: any = this.selectedAction == UserAction.Deposit ?
            localStorage.getItem("prevDepositPaymentMethod") : localStorage.getItem("prevWithdrawPaymentMethod");
        previousPaymentMethod =  previousPaymentMethod ? JSON.parse(previousPaymentMethod) as any: undefined;
        if (previousPaymentMethod) {
            if (previousPaymentMethod.fundsTransferType == "ach") {
                previousPaymentMethod = previousPaymentMethod as BankAccount;
            } else {
                previousPaymentMethod = previousPaymentMethod as CreditCard
            }
            if (this.isPaymentMethodActive(previousPaymentMethod)) {
                this.handleOnPaymentMethodClick(previousPaymentMethod)
            }
        } else { // if no selected payment method then just show payment methods view
            this.showModalView(this.paymentMethodsView);
        }
    }
    // check in linked credit cards and bank accounts if previous payment method exists (could be removed meanwhile)
    public isPaymentMethodActive(paymentMethod: CreditCard | BankAccount) {
        let found = false;
        if (paymentMethod.fundsTransferType == "ach") {
            this.linkedBankAccounts.forEach(bankAccount => { if (bankAccount.id == paymentMethod.id) found = true;})
        } else {
            this.linkedCreditCards.forEach(creditCard => { if (creditCard.id == paymentMethod.id) found = true;})
        }
        return found;
    }

    public showAddPaymentMethodView() {
        this.showModalView(this.paymentOptionsTypeView);
    }

    public async showPlaidLink(modal?: HTMLElement | null | undefined) {
        try {
            // hide modal before plaid pops up and show it again in the EXIT event (pl.onEvent)
            if (modal) {
                hideModalView(modal);
                this.previousView = modal;
            }
            hideModalView(this.activeView);
            const res = await this.bridge!.loadPlaid();
            log("Plaid link initialized");
            this.bridge!.plaidLinkHandler = res;

            if (this.bridge!.plaidLinkHandler) {
                this.bridge!.plaidLinkHandler.open();
            } else {
                log("this.bridge.plaidLinkHandler:", this.bridge!.plaidLinkHandler)
            }
        } catch (e) {
            this.handleError(e);
        }
    }

    private async showCreditCardWidget() {
        if (this.creditCardWidgetReady) {
            verificationCodeInterval = setInterval(() => this.getAndDisplayVerificationCode(), 5000);
            try {
                const res: AxiosResponse<CreditCardResourceResponse> = await this.bridge!.creditCardApiService.getCreditCardResourceTokenAndId();
                this.showModalView(this.creditCardModalView);
                this.bridge!.dispatchCreditCardWidgetInitEvent(this.creditCardWidget!, res.data.resourceTokenHash);
                this.activeCreditCardResourceId = res.data.creditCardResourceId;
            } catch (e) {
                this.handleError(e);
            }
        } else {
            this.handleError(new BridgeError("Credit card widget is not ready yet, go back and try again."))
        }
    }

    private getAndDisplayVerificationCode() {
        log("Interval triggered..");
        if (this.activeCreditCardResourceId && this.activeView == this.creditCardModalView) {
            this.bridge!.creditCardApiService.getCreditCardVerificationCode(this.activeCreditCardResourceId)
                .then((res: any) => {
                    if (res.data["verificationCode"]) {
                        log("Verification code:", res.data["verificationCode"]);
                        log("Clearing interval object: ", verificationCodeInterval);
                        clearInterval(verificationCodeInterval);
                        alert("Verification code = " + res.data["verificationCode"]);
                    }
                }).catch(e => {
                clearInterval(verificationCodeInterval);
                this.handleError(e);
            });
        }
    }

    backToHomeView() {
        this.dispatchEvent(new CustomEvent('backToHomeView', {}));
        // reset view
        this.showModalView(this.paymentMethodsView);
    }

    private handleOnPaymentMethodClick(selectedPaymentMethod: CreditCard | BankAccount) {
        this.selectedPaymentMethod = selectedPaymentMethod;
        if (this.selectedAction == UserAction.Deposit) {
            localStorage.setItem("prevDepositPaymentMethod", JSON.stringify(selectedPaymentMethod));
            this.showModalView(this.depositElem);
        } else {
            localStorage.setItem("prevWithdrawPaymentMethod", JSON.stringify(selectedPaymentMethod));
            this.showModalView(this.withdrawElem)
        }
    }

    private handleRemovePaymentMethodClick(selectedPaymentMethod: CreditCard | BankAccount) {
        this.selectedPaymentMethod = selectedPaymentMethod;
        this.showModalView(this.paymentRemoveView);
    }

    private async removePaymentMethod() {
        this.showModalView(this.processingRemoveView);
        try {
            await this.bridge!.accountApiService.deactivateFundsTransferMethod(this.selectedPaymentMethod!.id);
            if (isBankAccount(this.selectedPaymentMethod)) {
                this.linkedBankAccounts = this.linkedBankAccounts.filter( (bankAccount: BankAccount) => {
                    return bankAccount.id != this.selectedPaymentMethod.id;
                });
            } else {
                this.linkedCreditCards = this.linkedCreditCards.filter( (creditCard: CreditCard) => {
                    return creditCard.id != this.selectedPaymentMethod.id;
                });
            }
            this.showModalView(this.paymentMethodsView);
        } catch (e) {
            this.handleError(e);
        }
    }

    insertPlaidLinkFrontEndScript() {
        if(!this.plaidLinkScriptLoaded) {
            let script = document.createElement('script');
            this.plaidLinkScriptLoaded = true;
            script.src = environment.PLAID_LINK_WIDGET_URL;
            return script;
        }
    }

    insertPrimeTrustFrontEndScript() {
        if (!this.creditCardWidgetLoaded) {
            let script = document.createElement('script');
            script.src = environment.PRIME_TRUST_CREDIT_CARD_WIDGET_URL;
            this.creditCardWidgetLoaded = true;
            return script;
        }
    }

    render() {
        return html`    
    
                        <deposit-elem id="deposit-elem" hidden
                           @backToPaymentMethods="${() => this.showModalView(this.paymentMethodsView)}"
                           @backToHomeView="${this.backToHomeView}"
                          .bridge="${this.bridge}"
                          .user="${this.user}"
                          .userBalance="${this.userBalance}"
                          .selectedPaymentMethod="${this.selectedPaymentMethod}"
                          >
                        </deposit-elem>
                        
                        <withdraw-elem id="withdraw-elem" hidden
                           @backToPaymentMethods="${() => this.showModalView(this.paymentMethodsView)}"
                           @backToHomeView="${this.backToHomeView}"
                          .bridge="${this.bridge}"
                          .user="${this.user}"
                          .userBalance="${this.userBalance}"
                          .selectedPaymentMethod="${this.selectedPaymentMethod}"
                        >
                        
                        </withdraw-elem>
                        
    
                        <div class="widget" id="payment-methods" hidden>
                          <!-- Navigation -->
                          <div class="grid">
                            <div class="grid-cell">
                              <a @click="${this.backToHomeView}}" class="back">Back</a>
                            </div>
                            <div class="grid-cell">
                              <p class="label-upper text-center">Payment method</p>
                            </div>
                            <div class="grid-cell">
                        
                            </div>
                          </div>
                          <div>
                            <table class="payments-list">
                              <tbody>
                                <tr ?hidden="${this.linkedCreditCards.length == 0 || this.selectedAction == UserAction.Withdraw}">
                                  <td colspan="3" class="title padding-top-15">Cards</td>
                                </tr>
                                
                                  ${this.linkedCreditCards.map((creditCard: CreditCard) =>
                                    html `
                                    <tr ?hidden="${this.selectedAction == UserAction.Withdraw}" class="card ${creditCard.type == "VI" ? "visa": "mastercard"}">
                                      <td @click="${() => this.handleOnPaymentMethodClick(creditCard)}"><img src=${cardSvg}></td>
                                      <td @click="${() => this.handleOnPaymentMethodClick(creditCard)}"><span class="">${CodeLists.getCreditCardPrettyName(creditCard.type)}</span><br>
                                      <span class="label-small">Ending in ${creditCard.last4Digits}</span></td>
                                      <td><a @click="${() => this.handleRemovePaymentMethodClick(creditCard)}" class="text-red animation-underline">Remove</a></td>
                                    </tr>
                                  `)}
                                
                                
                                  <tr ?hidden="${this.linkedBankAccounts.length == 0}">
                                    <td colspan="3" class="title">Bank accounts</td>
                                  </tr>
                                   ${this.linkedBankAccounts.map((bankAccount: BankAccount) =>
                                    html `
                                    <tr class="bank">
                                        <td @click="${() => this.handleOnPaymentMethodClick(bankAccount)}"><img src=${bankSvg}></td>
                                        <td @click="${() => this.handleOnPaymentMethodClick(bankAccount)}">
                                        <span class="">Bank</span>
                                        <br><span class="label-small">Ending in ${bankAccount.last4Digits}</span></td>
                                        <td><a @click="${() => this.handleRemovePaymentMethodClick(bankAccount)}" class="text-red text-small animation-underline">Remove</a></td>
                                    </tr>
                                  `)}
                              </tbody>
                            </table>
                            <p class="text-center margin-top-25 margin-bottom-15">
                                <a @click="${() => {this.selectedAction == UserAction.Withdraw ? this.showPlaidLink(this.paymentMethodsView) : this.showModalView(this.paymentOptionsTypeView)}}" 
                                class="button">${this.selectedAction == UserAction.Withdraw ? "Link a bank account": "Add payment method"}</a>
                            </p>
                          </div>
                        </div>
                          
                          
                         <!-- Select type of payment option to add -->
                         <div class="widget" id="payment-options-type-view" hidden>
                          <!-- Navigation -->
                          <div class="grid">
                            <div class="grid-cell">
                              <a @click="${this.backToHomeView}" class="back">Back</a>
                            </div>
                            <div class="grid-cell">
                              <p class="label-upper text-center">Deposit</p>
                            </div>
                            <div class="grid-cell">
                        
                            </div>
                          </div>
                        
                          <div class="margin-top-25">
                            <div class="grid">
                              <div class="grid-cell">
                                <label>Choose a payment method</label>
                              </div>
                            </div>
                        
                            <div class="grid grid-center margin-top-35 margin-bottom-25">
                            <!--
                              <div class="grid-cell">
                                <a @click="${this.showCreditCardWidget}" class="deposit-type-option">
                                  <img src=${paymentOptionCardSvg} class="feature-icon">
                                  <p class="text-center">Credit or<br>debit card</p>
                                </a>
                              </div>
                              <div class="grid-cell or">
                                <p class="text-upper text-center"><span class="label-upper">Or</span></p>
                              </div>
                              -->
                              <div class="grid-cell">
                                <a @click="${() => this.showPlaidLink(this.paymentOptionsTypeView)}" class="deposit-type-option">
                                  <img src=${paymentOptionBankSvg} class="feature-icon">
                                  <p class="text-center">US bank account</p>
                                </a>
                              </div>
                            </div>
                          </div> 
                        </div>
                          
                          
                          <div class="widget" id="payment-remove" hidden>
                              <!-- Navigation -->
                              <div class="grid">
                                <div class="grid-cell">
                                  <a @click="${() => this.showModalView(this.paymentMethodsView)}" class="back">Back</a>
                                </div>
                                <div class="grid-cell">
                                  <p class="label-upper text-center">Remove</p>
                                </div>
                                <div class="grid-cell">
                            
                                </div>
                              </div>
                              <div class="grid grid-center margin-top-35">
                                <div class="grid-cell">
                                  <h2 class="margin-bottom-10">Remove this<br>payment method?</h2>
                                  <img src=${isBankAccount(this.selectedPaymentMethod) ? bankSvg: cardSvg} class="feature-icon">
                                  <p class="text-center margin-bottom-15">${isBankAccount(this.selectedPaymentMethod) ? "Bank account": "Card"} ending in 
                                  <span class="text-bold">${this.selectedPaymentMethod.last4Digits}</span></p>
                                </div>
                              </div>
                              <div class="grid grid-center">
                                <div class="grid-cell">
                                  <p class="text-center margin-top-15 margin-bottom-15"><a @click="${ () => this.showModalView(this.paymentMethodsView)}">Cancel</a></p>
                                </div>
                                <div class="grid-cell">
                                  <p class="margin-top-15 margin-bottom-15"><a @click="${() => this.removePaymentMethod()}" class="button">Remove</a></p>
                                </div>
                              </div>
                          </div>
                          
                        <div class="widget" id="processing-remove" hidden>
                          <div class="grid grid-center inherit-height">
                            <div class="grid-cell">
                              <img src=${isBankAccount(this.selectedPaymentMethod) ? bankSvg: cardSvg} class="feature-icon">
                              <h2 class="margin-bottom-10">Removing payment method</h2>
                              <p class="text-center margin-top-15 margin-bottom-10">This may take a few moments.</p>
                        
                              <!-- Loading animation -->
                              <div class="loading-animation">
                                <label></label>
                                <label></label>
                                <label></label>
                                <label></label>
                                <label></label>
                                <label></label>
                              </div>
                            </div>
                          </div>
                        </div>
                        
                        <div class="widget" id="loading-bank-acc" hidden>
                          <div class="grid grid-center inherit-height">
                            <div class="grid-cell">
                              <img src=${bankSvg} class="feature-icon">
                              <h2 class="margin-bottom-10">Loading your<br>bank account</h2>
                        
                              <!-- Loading animation -->
                              <div class="loading-animation">
                                <label></label>
                                <label></label>
                                <label></label>
                                <label></label>
                                <label></label>
                                <label></label>
                              </div>
                            </div>
                          </div>
                        </div>
                          
                          <div class="widget" id="error" hidden>
                              <div class="grid">
                                  <div class="grid-cell">
                                      <a @click="${() => this.showModalView(this.previousView!)}" class="back">Back</a>
                                      </div>
                                      <div class="grid-cell">
                                      </div>
                                      <div class="grid-cell">
                                  </div>
                              </div>
                              <div class="grid grid-center inherit-height">
                                <div class="grid-cell">
                                  <img src=${errorSvg} class="feature-icon">
                                  <h2>Error</h2>
                                  <p class="text-center margin-bottom-15">${this.error}</p>
                                </div>
                              </div>
                          </div>
                          
                          
                <!-- Primetrust: this is our "target" DOM node where the UI will be injected -->
                <div id="cc-widget-wrapper" hidden>
                    <div class="grid">
                      <div class="grid-cell">
                        <a @click="${this.creditCardWidgetBack}" class="back">Back</a>
                      </div>
                    </div>
                    <div style="max-height: 500px;margin: 0;padding: 0; overflow-y: scroll">
                     <div id="credit-card-target"></div>
                    </div>
                </div>    
  
                          
                <!-- download the Plaid link script -->
                ${this.insertPlaidLinkFrontEndScript()}
                
                <!-- download the Prime Trust application, this *must* come after the ready event listener has been added -->
                ${this.insertPrimeTrustFrontEndScript()}
        `}
}

declare global {
    interface HTMLElementTagNameMap {
        'payment-methods-elem': PaymentMethodsElem;
    }
}
