export enum CipExceptions {
    /*
   There was a failure when trying to verify the address provided for the contact.
   Update the Address on the contact. *Rerun on PATCH*
   Alternatively you can wait for a Prime Trust compliance officer to review the document
    */
    AddressCheck = "address_auto_check_failed",

    /*
    There was a failure when trying to manually verify the address provided for the contact.
    Update the Address on the contact.
    */
    AddressManualCheck = "address_manual_check_failed",
    /*
    There was a technical error in processing the cip-check.
    Prime Trust's compliance team will manually review the check (manual_review_required will also be present).None
    */
    AutoCheck = "auto_check_failed",
    /*
    There was a failure when trying to verify the date of birth for the contact.
    Update/check the date of birth for the contact.
    */
    DateOfBirthAutoCheck = "date_of_birth_auto_check_failed",
    /*
    There was a failure when trying to match the name provided to the rest of the information provided for the contact.Update/check the name for the contact.
    */
    NameAutoCheck = "name_auto_check_failed",
    /*
    There was a failure when trying to verify the tax-id number provided.Update/check the tax-id-number for the contact.
    */
    TaxIdAutoCheck = "tax_id_number_auto_check_failed",
    /*
    There was a failure when trying to manually verify the tax-id number provided for the contact.
    Update/check the tax-id-number for the contact.
    */
    TaxIdManualCheck = "tax_id_number_manual_check_failed",

    ManualReviewRequired = "manual_review_required"

}
