export enum KycDocCheckExceptions {
    /*
       There was a technical error and this document could not be automatically verified.
       The quickest way to resolve this is to upload a new document or generate a new kyc document check on the existing document.
       Alternatively you can wait for a Prime Trust compliance officer to review the document
     */
    AutoCheck = "auto_check_failed",

    /* The document is being reviewed manually by a Prime Trust compliance officer. */
    ManualCheck = "manual_review_check_failed",

    /*
        The date of birth on the document does not match the date of birth listed on the contact.
        Update the date of birth on the contact or provide a new document which lists the date of birth provided on the contact.
     */
    DateOfBirth = "date_of_birth_auto_check_failed",

    /* The document provided is not valid (for example is expired or is fake). Provide a new valid document. */
    DocumentInvalid = "document_valid_auto_check_failed",

    /*
        The country that issued the id does not match the tax country listed on the contact.
        Provide a new document that matches the tax country specified on the contact or update
        the tax country on the contact and generate a new kyc document check.
     */
    TaxCountry = "tax_country_auto_check_failed",

    /*
        The address on the document did not match the address on the contact.
        Either update the address on the contact if possible or request for a proof of address.
     */
    AddressAutoCheck = "address_auto_check_failed",

    WaitingForDocumentUploads = "waiting_for_document_uploads"
}




