/*
 * Utility functions for bridge web component
 */

import {NaturalPersonContactUS} from "../models/PrimeTrust/NaturalPersonContactUS";
import {Consts} from "./consts";
import {environment} from "../../environment/environment";
import {BankAccount} from "../models/bankAccount/BankAccount";
import {CreditCard} from "../models/CreditCard/CreditCard";
import {CodeLists} from "./code-lists";

export function icxValueToNormalValue(icxValue: number): number {
    return +(icxValue / 1e18).toFixed(2);
}

export function parseHexToNumber(value: string | number): number {
    if (typeof value === 'string')
        return parseInt(value, 16)
    else
        return value;
}

export function instanceOfNaturalPersonContactUS(object: any): object is NaturalPersonContactUS {
    return object instanceof NaturalPersonContactUS;
}

export function extractDidEthAddress(issuer: string | null): string {
    if (!issuer) return "";
    return issuer.split(":")[2].trim();
}

export function extractFileExtension(file: string | File): string {
    if (file instanceof File)
        return String(file.name.split(".").pop());
    else
        return file.split(".").pop()!
}

export function stringToDateString(date: string): string {
    return new Date(date).toDateString();
}

// parameter = user input amount, returns amount with credit card fee included and rounded to 2 decimals
export function calculateCredCardTotalAmount(amount: number): number {
    if (!amount) return 0;
    return roundUpToTwoDecimals(amount + calculateCreditCardFee(amount))
}

// returns 4.5% fee rounded up, fix to 5 decimal point to avoid floating point issues (e.g. 14 -> 0.6300000000000001)
export function calculateCreditCardFee(amount: number): number {
    if (!amount) return 0;
    return roundUpToTwoDecimals(+((amount / 100) * Consts.PrimeTrustCreditCardFee).toFixed(5));
}

export function roundUpToTwoDecimals(number: number): number {
    return Math.ceil(number * Math.pow(10, 2)) / Math.pow(10, 2);
}

export function extractMessageFromError(error: any): string {
    if (error && error.response && error.response.data) return String(error.response.data.message);
    else return String(error);
}

export function extractDetailMessageFromError(error: any): string {
    if (error?.response?.data?.errors?.length > 0) {
        let detail = error.response.data.errors[0].detail;
        if (error.response.data.errors[0].source?.pointer) {
            const splitPointer: string[] = error.response.data.errors[0].source.pointer.split("/");
            const pointer = splitPointer.length > 0 ? splitPointer[splitPointer.length - 1] : ""
            detail = "reason: " + pointer + " , detail: " + detail;
        }
        return detail;
    }
    else
        return "";
}


export function log(message: any, ...args: any) {
    // TODO consider using dedicated logging lib
    if (!environment.production) console.log(message);
    args.forEach((arg: any) => console.log(arg));
}

export function isBankAccount(selectedPaymentMethod: BankAccount | CreditCard): boolean {
    return selectedPaymentMethod.fundsTransferType == "ach";
}

// extract either bank name or card name from selected payment method
export function extractPaymentMethodName(selectedPaymentMethod: BankAccount | CreditCard): string {
    if (isBankAccount(selectedPaymentMethod)) {
        return "Bank";
    } else {
        return CodeLists.getCreditCardPrettyName(selectedPaymentMethod.type);
    }
}

// format number so that thousands are separated by commas
export function numberWithCommas(amount: string) {
    if (amount !== '' || amount !== undefined || amount !== 0  || amount !== '0' || amount !== null) {
        return amount.replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",");
    } else {
        return amount;
    }
}
